// ----------------------------- EntityCache

class EntityCacheEntry {
	constructor(inTimeStamp, inRawEntry, inStamp) {
		this.fTimeStamp = inTimeStamp; // a date
		this.fRawEntry = inRawEntry;
		this.fStamp = inStamp;

	}

	GetTimeStamp() {
		return this.fTimeStamp;
	}
}

class EntityCache {

	constructor() {
		this.fMaxEntities = 30000;
		this.fTimeOut = 30 * 1000; // 30 sec
		this.fCurStamp = 0;
		this.fCache = {};
	}


	getNextStamp()
	{
		this.fCurStamp += 1;
		return this.fCurStamp;
	}

	makeRoomFor(nbEntites) {
		if (this.fMaxEntities < (nbEntites * 2)) {
			this.fMaxEntities = Math.round(nbEntites * 2);
		}
		const remain = this.fMaxEntities - Object.keys(this.fCache).length;
		if (remain < nbEntites) {
			this.clear(nbEntites - remain);
		}
	}

	clear(nbToClear) {
		const all = Object(this.fCache).entries();
		all.sort((e1, e2) => {
			if (e1.value.fTimeStamp < e2.value.fTimeStamp)
				return -1;
			if (e1.value.fTimeStamp > e2.value.fTimeStamp)
				return 1;
			return 0;
		});

		let nbelem = nbToClear;
		if (nbelem > all.length)
			nbelem = all.length;

		for (let i = 0; i < nbelem; i+=1)
		{
			delete this.fCache.erase(all[i].key);
		}

	}

	clearAll() {
		this.fCache = {};
	}

	setEntry(inKey, inRawEntry, inTimeStamp) {
		const newStamp = this.getNextStamp();
		const entry = this.fCache[inKey];
		if (entry == null) {
			const nbEntries = Object.keys(this.fCache).length;
			if (nbEntries >= this.fMaxEntities) {
				this.clear(nbEntries / 3);
			}
			this.fCache[inKey] = new EntityCacheEntry(inTimeStamp, EntityCache.cloneRawEntity(inRawEntry), newStamp);
		}
		else {
			const alreadyEntry = entry.fRawEntry;
			let canmerge = false;
			if (alreadyEntry != null && inRawEntry != null) {
				if (alreadyEntry.__STAMP === inRawEntry.__STAMP)
					canmerge = true;

			}

			if (canmerge) {
				const clonenewentry = EntityCache.cloneRawEntity(inRawEntry);
				Object.keys(clonenewentry).forEach((attr) => {
					alreadyEntry[attr] = clonenewentry[attr];
				});
			}
			else {
				entry.fRawEntry = EntityCache.cloneRawEntity(inRawEntry);
			}
			entry.fStamp = newStamp;
			entry.fTimeStamp = inTimeStamp;
		}

	}

	retainEntry(inKey, inCacheDelay) {
		const entry = this.fCache[inKey];
		let result = null;

		if (entry != null) {
			const now = EntityCache.getTimeStamp();
			if ((now - entry.fTimeStamp) <= inCacheDelay) {
				result = EntityCache.cloneRawEntity(entry.fRawEntry);
			}
			else {
				delete this.fCache[inKey];
			}
		}
		return result;

	}

	removeEntry(inKey) {
		delete this.fCache[inKey];
	}

	setMaxEntries(nbentries, canReduce) {
		let nb = nbentries;
		if (nb < 300)
			nb = 300;
		if (canReduce || nb > this.fMaxEntities)
			this.fMaxEntities = nb;
	}

	setTimeOut(inTimeout) {
		this.fTimeOut = inTimeout;
	}
}


EntityCache.cloneRawEntity = (rawEntityDef) => {
	// may later do a real clone, if needed
	return rawEntityDef;
};

EntityCache.getTimeStamp = () => {
	return new Date();
};

module.exports = {
	EntityCache,
	EntityCacheEntry,
};
