// import moment from 'moment';
const moment = require('moment');

String.prototype.capitalize = function capitalize() {
	if (this.length > 1) {
		return this[0].toUpperCase() + this.substring(1).toLowerCase();
	}
	return this.toUpperCase();
};

String.prototype.capitalizeEachWord = function capitalizeEachWord() {
	const result = this.split(' ');
	for (let i = 0, nbElems = result.length; i < nbElems; i+=1) {
		const elem = result[i];
		if ((typeof elem === 'string') || (elem instanceof String))
			result[i] = elem.capitalize();
	}

	return result.join(' ');
};


class Utils {

	static stringToNumber(text) {
		let newtext = '';
		text.split('').forEach((c) => {
			if ((c >= '0' && c <= '9') || (c === '+') || (c === '-') || (c === '.') || (c === ',') || (c === 'e') || (c === 'E')) {
				if (c === ',')
					newtext += '.';
				else
					newtext += c;
			}
		});
		let result;
		try {
			result = parseFloat(newtext);
		}
		catch (err) {
			result = null;
		}
		return result;
	}

	static getBoolValue (text) {
		if (text == null)
			return false;

		if (typeof text === 'string') {
			if (text.toLowerCase() === 'true')
				return true;
			return false;
		}
		return text;

	};

	static getBrowserLang() {
		const lang = (navigator.language || navigator.systemLanguage || navigator.userLanguage || 'us').substr(0, 2).toLowerCase();
		return lang;
	};

	static stringStartsWith(s1, s2) {
		const lang = Utils.getBrowserLang();
		if (s2.length > s1.length)
			return false;
		else
			return s1.substring(0,s2.length).localeCompare(s2, lang, { sensitivity: 'base' }) === 0;
	}

	static customStringFormatter(value, options) {
		// Use # as placeholder, other chars will be inserted as is... (ie: "(###) ###-###" )
		const formatter = {
			original: value,
			string: value,
			format: options.format || '',
			formatType: null,
			formatExtremes: null,
			formatDecimals: 0,
			formatDividors: [],
			detectFormat(string) {
				return /([.,])/.test(string)
					? 'Number'
					: 'String';
			},
			getFormatExtremes(format) {
				return [
					format.substr(0, format.indexOf('#')),
					format.substr(format.indexOf('#'), format.lastIndexOf('#') - format.indexOf('#') + 1),
					format.substr(format.lastIndexOf('#') + 1, format.length),
				];
			},
			getFormatDecimals(format) {
				// retrieve the decimal numbers count from the format custom string
				return format.length - format.lastIndexOf('.') - 1;
			},
			getFormatDividors(format) {
				// get all special dividors in format string
				const dividors = [];
				for (let i = 0; i < format.length; i += 1) {
					if (format[i] !== '#') {
						if (dividors.indexOf(format[i]) < 0)
							dividors[dividors.length] = format[i];
					}
				}
				return dividors;
			},
			fixStringDecimals(string, decimals) {
				let s = string;
				// if the number is < than 0, add the 0, before it
				if (string.length === decimals) {
					s = `0${string}`;
				} else if (string.length < decimals) {
					for (let i = string.length; i <= decimals; i += 1) {
						s = `0${string}`;
					}
				}
				return s;
			},
			removeStringDividorsCharacters(string) {
				return string.replace(/([.,])/g, '');
			},
			formatStringFromRightToLeft(string, format) {
				let s = string;
				// insert the extra char starting from right
				for (let i = format.length - 1; i > 0; i -= 1) {
					if (string.length - (format.length - i - 1) > 0 && format[i] !== '#') {
						s = [
							string.slice(0, string.length - (format.length - i - 1)),
							format[i],
							string.slice(string.length - (format.length - i - 1)),
						].join('');
					}
				}
				return s;
			},
			formatStringFromLeftToRight(string, format) {
				// insert the extra char starting from left
				let result = ''; let j = 0;
				for (let i = 0; i < format.length; i += 1) {
					result += format[i] === '#' ? (string[j += 1] || '') : format[i];
				}

				result += string.slice(j);
				return result;
			},
			getResult() {
				return this.formatExtremes[0] + this.string + this.formatExtremes[2];
			},
		};

		// Format string main parts
		// Divide the format string in 3, the interesting part is always the middle one [1]
		formatter.formatExtremes = formatter.getFormatExtremes(formatter.format);

		// Format string decimal counts
		// count the number of decimal value after the . the user wants (ex: #.### equals 3 decimal values before zero)
		formatter.formatDecimals = formatter.getFormatDecimals(formatter.formatExtremes[1]);

		// Dividors (,.) in format string
		// get once every , . character
		formatter.formatDividors = formatter.getFormatDividors(formatter.formatExtremes[1]);

		// Try to figure out format type from format string
		formatter.formatType = formatter.detectFormat(formatter.formatExtremes[1]);

		switch (formatter.formatType) {
			case 'Number':
			// Number behavior - like format (Currency, Numbers, etc)

				// - remove unwanted , or .
				// the user digits 4.20, remove the . so it becomes 420 and the format it back to 4.20
				formatter.string = formatter.removeStringDividorsCharacters(formatter.string);

				// - fix decimals when value < 0
				// example: if format #,### and value equals 2 then fix it like 0002
				formatter.string = formatter.fixStringDecimals(formatter.string);

				// - format direction Right to Left
				// example: any decimal number, ex: #,## means 420 becomes 4.20 as 1420 becomes 14.20
				formatter.string = formatter.formatStringFromRightToLeft(formatter.string, formatter.formatExtremes[1]);
				break;

			case 'String':
			default:
			// Literal behavior - like format (Phonenumbers, Emails, etc)
			// - format direction Left to Right
			// example: telephone number +33 (##) ########
				formatter.string = formatter.formatStringFromLeftToRight(formatter.string, formatter.formatExtremes[1]);
				break;

		}
		return formatter.getResult();
	};


	static formatString(value, options) {
		let result;
		let o = options;
		if (options == null || value == null || value === '') {
			result = value;
		}
		else {
			if (typeof options === 'string')
				o = { format: options };
			switch (o.format) {
				case 'U':
					result = value.toUpperCase();
					break;
				case 'l':
					result = value.toLowerCase();
					break;
				case 'C':
					result = value.capitalize();
					break;
				case 'c':
					result = value.capitalizeEachWord();
					break;
				default:
					result = Utils.customStringFormatter(value, options);
					break;
			}
		}

		return result;
	};


	static formatDate(value, xoptions = { format: '' }) {
		let v = value;
		let options;
		if (typeof xoptions === 'string')
			options = { format: xoptions };
		else
			options = { format: xoptions.format, locale: xoptions.locale };

		if (options.locale == null)
			options.locale = Utils.getBrowserLang();

		let settings = null;
		if (options.locale != null) {
			if (options.locale === 'us' || options.locale === 'en')
				options.locale = '';
			settings = $.datepicker.regional[options.locale];
			if (settings != null) {
				if ((options.format == null || options.format === '') && settings.dateFormat != null)
					options.format = settings.dateFormat;
			}
		}

		if (options.format == null || options.format === '') {
			options.format = 'mm/dd/yy';
		}

		if (typeof value === 'string') {
			v = new Date(value);
		}

		if (options.format.indexOf('l') === -1 && options.format.indexOf('L') === -1)
			return $.datepicker.formatDate(options.format, v, settings);
		else {
			const m = moment(v);
			m.locale(options.locale);
			return m.format(options.format);
		}
	};

	static formatDuration(value /* , format */) {
		if (value == null)
			return '';
		if (value !== 0)
			value /= 1000;
		const secs = value % 60;
		const mins = Math.floor((value % 3600) / 60);
		const hours = Math.floor(value / 3600);
		let secstr = String(secs);
		if (secstr.length === 1)
			secstr = '0' + secstr;
		let minstr = String(mins);
		if (minstr.length === 1)
			minstr = '0' + minstr;
		let hourstr = String(hours);
		if (hourstr.length === 1)
			hourstr = '0' + hourstr;
		return hourstr + ':' + minstr + ':' + secstr;
	}

	static stringToDuration(text) {
		let result = null;
		if (text != null) {
			const [part1, part2, part3] = text.split(':').map(part => parseInt(part, 10));
			if (part3 != null)
				result = (part3 + part2 * 60 + part1 * 3600) * 1000;
			else if (part2 != null)
				result = (part2 + part1 * 60) * 1000;
			else if (part1 != null)
				result = part1 * 1000;
			else result = null;
			if (result != null && Number.isNaN(result))
				result = null;
		}
		return result;
	}


	static formatNumber(value, xoptions) {
		let o = xoptions;

		function FormatData(dec, group, neg) {
			this.dec = dec;
			this.group = group;
			this.neg = neg;
		};

		function formatCodes(locale, overrideGroup = false) {
			let dec = '.';
			let group = ',';
			const neg = '-';

			if (locale === 'us' ||
				locale === 'ae' ||
				locale === 'eg' ||
				locale === 'il' ||
				locale === 'jp' ||
				locale === 'sk' ||
				locale === 'th' ||
				locale === 'cn' ||
				locale === 'hk' ||
				locale === 'tw' ||
				locale === 'au' ||
				locale === 'ca' ||
				locale === 'gb' ||
				locale === 'in'
			) {
				dec = '.';
				group = ',';
			}

			else if (locale === 'de' ||
				locale === 'vn' ||
				locale === 'es' ||
				locale === 'dk' ||
				locale === 'at' ||
				locale === 'gr' ||
				locale === 'br'
			) {
				dec = ',';
				group = '.';
			}
			else if (locale === 'cz' ||
				locale === 'fr' ||
				locale === 'fi' ||
				locale === 'ru' ||
				locale === 'se'
			) {
				group = ' ';
				dec = ',';
			}
			else if (locale === 'ch') {
				group = '\'';
				dec = '.';
			}

			if (overrideGroup)
				group = ' ';
			return new FormatData(dec, group, neg);
		};

		if (typeof xoptions === 'string')
			o = { format: xoptions };

		if (xoptions == null) {
			o = { format: '' };
		}
		const options = { format: o.format, locale: o.locale, overrideGroup: o.overrideGroup};

		if (options.locale == null)
			options.locale = Utils.getBrowserLang();

		let returnString = String(value);
		if (options.format == null || options.format === '')
			return returnString;

		const formatData = formatCodes(options.locale.toLowerCase(), options.overrideGroup);

		const { dec } = formatData;
		const { group } = formatData;
		const { neg } = formatData;

		const validFormat = '0#-,.';

		let prefix = '';
		let negativeInFront = false;
		for (let i = 0; i < options.format.length; i += 1) {
			if (validFormat.indexOf(options.format.charAt(i)) === -1)
				prefix += options.format.charAt(i);
			else if (i === 0 && options.format.charAt(i) === '-') {
				negativeInFront = true;
			}
			else
				break;
		}
		let suffix = '';
		for (let i = options.format.length - 1; i >= 0; i -= 1) {
			if (validFormat.indexOf(options.format.charAt(i)) === -1)
				suffix = options.format.charAt(i) + suffix;
			else
				break;
		}
		options.format = options.format.substring(prefix.length);
		options.format = options.format.substring(0, options.format.length - suffix.length);
		let number = value;

		if (suffix === '%')
			number *= 100;

		returnString = '';

		const decimalValue = number % 1;
		if (options.format.indexOf('.') > -1) {
			let decimalPortion = dec;
			const decimalFormat = options.format.substring(options.format.lastIndexOf('.') + 1);
			let decimalString = decimalValue.toFixed(decimalFormat.length);
			decimalString = decimalString.substring(decimalString.lastIndexOf('.') + 1);

			for (let i = 0; i < decimalFormat.length; i += 1) {
				if (decimalFormat.charAt(i) === '#' && decimalString.charAt(i) !== '0') {
					decimalPortion += decimalString.charAt(i);
					// don't know why there is a break here so..'
					// break;
				}
				else if (decimalFormat.charAt(i) === '0') {
					decimalPortion += decimalString.charAt(i);
				}
			}
			if (decimalPortion !== dec)
				returnString += decimalPortion;
		}
		else
			number = Math.round(number);

		let ones = Math.floor(number);
		if (number < 0)
			ones = Math.ceil(number);

		let onePortion = '';
		if (ones === 0) {
			onePortion = '0';
			if (number < 0)
				onePortion = '-0';
		}
		else {
			let onesFormat = '';
			if (options.format.indexOf('.') === -1)
				onesFormat = options.format;
			else
				onesFormat = options.format.substring(0, options.format.indexOf('.'));
			const oneText = `${ones}`;
			let groupLength = 9999;
			if (onesFormat.lastIndexOf(',') !== -1)
				groupLength = onesFormat.length - onesFormat.lastIndexOf(',') - 1;
			let groupCount = 0;
			for (let i = oneText.length - 1; i > -1; i -= 1) {
				onePortion = oneText.charAt(i) + onePortion;

				groupCount += 1;

				if (groupCount === groupLength && i !== 0 && oneText.charAt(i - 1) !== '-') {
					onePortion = group + onePortion;
					groupCount = 0;
				}

			}
		}

		returnString = onePortion + returnString;

		if (number < 0 && negativeInFront && prefix.length > 0) {
			returnString = returnString.substring(1);
			prefix = neg + prefix;
		}

		returnString = prefix + returnString + suffix;

		return returnString;

	};

	static htmlDecode(value) {
		if (value && (value === '&nbsp;' || value === '&#160;' || (value.length === 1 && value.charCodeAt(0) === 160))) {
			return ' ';
		}

		return !value ? value : String(value).replace(/&amp;/g, '&').replace(/&gt;/g, '>').replace(/&lt;/g, '<').replace(/&quot;/g, '"');
	};



	static init() {
		const lang = Utils.getBrowserLang();
		let dateOptions = null;
		if (typeof $ !== 'undefined' && $ != null && $.datepicker != null)
			dateOptions = $.datepicker.regional[lang];
		if (dateOptions != null) {
			$.datepicker.setDefaults(dateOptions);
			Utils.dateInputFormat = dateOptions.dateFormat || 'm/d/yy';
		}
		else
			Utils.dateInputFormat = 'm/d/yy';
	};

	static htmlEncode(source, display, tabs) {
		function special(src) {
			let result = '';
			for (let i = 0; i < src.length; i += 1) {
				let c = src.charAt(i);
				if (c < ' ' || c > '~') {
					c = `&#${c.charCodeAt()};`;
				}
				result += c;
			}
			return result;
		}

		function format(src) {
			// Use only integer part of tabs, and default to 4
			const tabs1 = (tabs >= 0) ? Math.floor(tabs) : 4;

			// split along line breaks
			const lines = src.split(/\r\n|\r|\n/);

			// expand tabs
			for (let i = 0; i < lines.length; i += 1) {
				const line = lines[i];
				let newLine = '';
				for (let p = 0; p < line.length; p += 1) {
					const c = line.charAt(p);
					if (c === '\t') {
						const spaces = tabs1 - (newLine.length % tabs1);
						for (let s = 0; s < spaces; s += 1) {
							newLine += ' ';
						}
					}
					else {
						newLine += c;
					}
				}
				// If a line starts or ends with a space, it evaporates in html
				// unless it's an nbsp.
				newLine = newLine.replace(/(^ )|( $)/g, '&nbsp;');
				lines[i] = newLine;
			}

			// re-join lines
			let result = lines.join('<br />');

			// break up contiguous blocks of spaces with non-breaking spaces
			result = result.replace(/ {2}/g, ' &nbsp;');

			// tada!
			return result;
		}

		let result = source;

		// ampersands (&)
		result = result.replace(/&/g, '&amp;');

		// less-thans (<)
		result = result.replace(/</g, '&lt;');

		// greater-thans (>)
		result = result.replace(/>/g, '&gt;');

		if (display) {
			// format for display
			result = format(result);
		}
		else {
			// Replace quotes if it isn't for display,
			// since it's probably going in an html attribute.
			result = result.replace(new RegExp('"', 'g'), '&quot;');
		}

		// special characters
		result = special(result);

		// tada!
		return result;
	};



	static formatFixInteger(n, leadingZeros) {
		let s = String(n);
		for (let i = s.length; i < leadingZeros; i += 1) {
			s = `0${s}`;
		}
		return s;
	};

	static formatValueForDateInput(value, internalFormat = false) {
		if (value == null)
			return '';
		let d = null;
		if (value instanceof Date)
			d = value;
		else
			d = new Date(value);
		const s1 = Utils.formatFixInteger(d.getFullYear(), 4);
		const s2 = Utils.formatFixInteger(d.getMonth() + 1, 2);
		const s3 = Utils.formatFixInteger(d.getDate(), 2);
		// return `${s1}-${s2}-${s3}`;
		if (internalFormat)
			return `${s3}!${s2}!${s1}`;
		return `${s3}/${s2}/${s1}`;
	};


	static parseDate(str) {
		const parts = str.split('!');
		if (parts.length === 3) {
			const year = parseInt(parts[2], 10);
			const month = parseInt(parts[1], 10);
			const day = parseInt(parts[0], 10);
			if (day === 0 || month === 0 || year === 0)
				return null;
			return new Date(year, month - 1, day);
		}
		return new Date(str);

	};

	static DecodeObjectFromData(datatext) {
		const obj = {};
		const parts = datatext.split(';');
		parts.forEach((part) => {
			const subparts = part.split(':');
			// Make eslint happy, use destructing operator
			([, obj[subparts[0]]] = subparts);
		});
		return obj;
	};

	static DecodeCollectionFromData(datatext) {
		const col = [];
		const parts = datatext.split('|');
		parts.forEach((part) => {
			const obj = Utils.DecodeObjectFromData(part);
			col.push(obj);
		});
		return col;
	};

	static formatValue(value, formatStr) {

		if (formatStr == null) {
			return String(value);
		}

		if (value == null)
			return '';

		if (typeof (value) === 'number') {
			return Utils.formatNumber(value, { format: formatStr });
		}

		if (value instanceof Date) {
			return Utils.formatDate(value, { format: formatStr });
		}

		if (typeof (value) === 'string') {
			return Utils.formatString(value, { format: formatStr });
		}

		return String(value);
	}

}

const dlog = (message, filter) => {
	if (window.debugIsOn) {
		if (filter == null || window.allowedFilter[filter]) {
			console.info(message);
		}
	}
};

module.exports = {
	dlog,
	Utils,
};
