// ------------------------------------------------------------- REST

class RestError extends Error {
	constructor(message, status, statusText, errorStack, errCode) {
		super(message);
		this.status = status;
		this.statusText = statusText;
		this.errorStack = errorStack;
		this.errCode = errCode;
	}

	getDescription() {
		let result = '';
		if (this.message != null)
			result += this.message;
		if (this.status != null)
			result += `, status code: ${  this.status}`;
		if (this.statusText != null)
			result += `, ${  this.statusText}`;
		return result;
	}
}

function restRequest(command, url, body) {
	if (!body)
		body = null;
	if (body != null) {
		if (body instanceof Object)
			body = JSON.stringify(body);
	}
	return new Promise((resolve, reject) => {

		const req = new XMLHttpRequest();
		req.open(command, url);

		req.onload = () => {

			if (req.status === 200) {
				let resp = req.responseText;
				try {
					const respobj = JSON.parse(req.responseText);
					resp = respobj;
				} catch (err) {
					// Do nothing
				}
				resolve(resp);
			}
			else {
				let alreadyReject = false;

				try {
					const obj = JSON.parse(req.responseText);
					if (obj != null && obj.__ERROR != null) {
						if (obj.__ERROR.length > 0) {
							alreadyReject = true;
							reject(new RestError(obj.__ERROR[0].message, req.status, req.statusText, obj.__ERROR, obj.__ERROR[0].errCode));
						}
					}
				} catch (err) {
					// Do nothing
				}

				if (!alreadyReject)
					reject(new RestError('Rest Error', req.status, req.statusText, null, null));
			}
		};

		req.onerror = function reqOnerr(err) {
			reject(new RestError('Network Error', -1, '', [{ errCode:-2, nativeError : err}], null));
		};

		req.send(body);
	});
}

window.restRequest = restRequest;

module.exports = {
	RestError,
	restRequest,
};
