// ----------------------------- Entity
const { Utils } = require('./tools');
const { restRequest, RestError } = require('./rest');

// ---------------------

class EntityValue {
	constructor(entity, attribute, initialValue) {
		if (initialValue === undefined)
			initialValue = null;
		let val = initialValue;
		if (attribute.kind === 'storage') {
			if (attribute.type === 'date') {
				if (typeof val === 'string')
					val = Utils.parseDate(val);
			}
		} else if (attribute.kind === 'relatedEntity') {
			if (initialValue == null) {
				val = null;
			} else {
				const datastore = entity.getDataClass().getDataStore();
				const relatedDataclass = datastore[attribute.type];
				// eslint-disable-next-line no-use-before-define
				val = new Entity(relatedDataclass, initialValue, null).buildProxy();
			}
		} else if (attribute.kind === 'relatedEntities') {
			if (initialValue == null)
				val = null;
			else {
				const datastore = entity.getDataClass().getDataStore();
				const relatedDataclass = datastore.getDataClassByCollectionName(attribute.type);
				// eslint-disable-next-line global-require
				const { EntitySelection } = require('./entitysel');

				val = new EntitySelection(relatedDataclass, null, initialValue);
			}

		}
		this.curValue = val;
		this.initialValue = val;
		this.touched = false;
		this.owner = entity;
		this.attribute = attribute;
	}

	exportToJSON() {
		let result = this.curValue;
		if (result instanceof Date) {
			const day = result.getDate();
			const month = result.getMonth()+1;
			const year = result.getFullYear();
			result = `${  day  }!${  month  }!${  year}`;
		}
		return result;
	}
}

const EntityProxy = {
	get (entity, prop) {
		if (prop === '_private') {
			return entity[prop];
		}
		const att = entity.getDataClass().getAttribute(prop);
		if (att != null) {
			const val = entity._private.values[prop];
			if (val == null)
				return null;
			return val.curValue;
		}

		return entity[prop];


	},

	set (entity, prop, value) {
		if (prop === '_private') {
			entity[prop] = value;
			return true;
		}
		const att = entity.getDataClass().getAttribute(prop);
		if (att != null) {
			let val = entity._private.values[prop];
			if (val == null) {
				val = new EntityValue(entity, att, null);
				entity._private.values[prop] = val;
			}
			val.touched = true;
			val.curValue = value;
			return true;
		}

		throw new Error('non existing attribute');
	},
};





// ---------------------


class Entity {
	constructor(owner, rawDef, settings, ownerSel, posInSel) {
		if (!settings)
			settings = null;
		const entity = this;
		let primKey = null;
		let stamp = 0;
		let inited = true;
		if (rawDef != null) {
			if (rawDef.__deferred != null) {
				primKey = rawDef.__deferred.__KEY;
				inited = false;
			} else {
				primKey = rawDef.__KEY;
				stamp = rawDef.__STAMP;
			}
		}

		this._private = {
			dataclass: owner,
			key: primKey,
			settings,
			inited,
			ownerSel,
			posInSel,
			stamp,
			filterAttributes: null,
		};

		if (settings != null) {
			if (settings.filterAttributes != null)
				entity._private.filterAttributes = settings.filterAttributes;
		}

		entity._private.values = {};
		if (this._private.inited) {
			const attributes = owner.getAllAttributes();
			Object.keys(attributes).forEach((e) => {
				entity._private.values[e] = new EntityValue(entity, owner.getAttribute(e), rawDef == null ? null : rawDef[e]);
			});
		}

		if (rawDef != null && primKey != null) {
			owner.getCache().setEntry(primKey, rawDef, new Date());
		}
	}

	buildProxy() {
		const proxy = new Proxy(this, EntityProxy);
		this._private.proxy = proxy;
		return proxy;
	}

	getKey() {
		return this._private.key;
	}

	getStamp() {
		return this._private.stamp;
	}

	getDataClass() {
		return this._private.dataclass;
	}

	getSelection() {
		return this._private.ownerSel;
	}

	getPos() {
		return this._private.posInSel;
	}

	fetch() {
		const entity = this;
		return new Promise((resolve, reject) => {
			if (entity._private.inited) {
				resolve(entity._private.proxy);
			} else {
				entity._private.dataclass.getEntity(entity._private.key, { filterAttributes: entity._private.filterAttributes }).then((newentity) => {
					entity._private = newentity._private;
					resolve(entity._private.proxy);
				}).catch(reject);
			}
		});
	}

	drop() {
		const entity = this;
		const priv = entity._private;

		const promisefunc = (resolve, reject) => {
			// entity et priv sont stockee dans la closure
			let url = `/rest/${  priv.dataclass.getName()}`;
			const {key} = priv;
			if (key != null) {
				url += `(${  key  })`;

				url += '?$method=delete';
				restRequest('post', url, null).then(() => {
					const dataclass = entity.getDataClass();
					dataclass.getCache().removeEntry(key);
					priv.key = null;
					resolve();
				}).catch(reject);
			} else {
				reject(new RestError('Cannot drop a new entity', 404));
			}
		};

		return new Promise(promisefunc);
	}

	fillObjectWithModifiedValues(body) {
		const entity = this;
		const priv = entity._private;
		const vals = priv.values;

		if (priv.key != null)
			body.__KEY = priv.key;
		if (priv.dataclass != null)
			body.__DATACLASS = priv.dataclass.getName();

		Object.keys(vals).forEach((e) => {
			const val = vals[e];
			if (val != null) {
				if (val.touched) {
					if (val.attribute.kind === 'storage' || val.attribute.kind === 'calculated') {
						body[val.attribute.name] = val.exportToJSON();
					} else if (val.attribute.kind === 'relatedEntity') {
						const relatedEntity = val.curValue;
						if (relatedEntity == null) {
							body[val.attribute.name] = null;
						} else if (relatedEntity._private.inited) {
							const relatedKey = relatedEntity.getKey();
							if (relatedKey != null) {
								body[val.attribute.name] = { '__KEY': relatedKey };
							}
						}
					}

				}
			}
		});

	}

	save() {
		const entity = this;
		const priv = entity._private;
		// const vals = priv.values;
		return new Promise((resolve, reject) => {
			let url = `/rest/${  priv.dataclass.getName()}`;
			const body = { __STAMP: priv.stamp };

			const {key} = priv;
			if (key != null) {
				url += `(${  key  })`;
				// body.__KEY = key;
			}

			const filter = priv.filterAttributes;
			if (filter != null && filter !== '') {
				url += `?$attributes=${filter}`;
			}
			entity.fillObjectWithModifiedValues(body);
			/*
			Object.keys(vals).forEach((e) => {
				const val = vals[e];
				if (val != null) {
					if (val.touched) {
						if (val.attribute.kind === 'storage' || val.attribute.kind === 'calculated') {
							body[val.attribute.name] = val.exportToJSON();
						} else if (val.attribute.kind === 'relatedEntity') {
							const relatedEntity = val.curValue;
							if (relatedEntity == null) {
								body[val.attribute.name] = null;
							} else if (relatedEntity._private.inited) {
								const relatedKey = relatedEntity.getKey();
								if (relatedKey != null) {
									body[val.attribute.name] = { '__KEY': relatedKey };
								}
							}
						}

					}
				}
			});
			*/

			restRequest('post', url, body).then((resp) => {
				const rawDef = resp;
				if (rawDef != null) {
					const owner = entity.getDataClass();
					const attributes = owner.getAllAttributes();
					Object.keys(attributes).forEach((e) => {
						const data = rawDef[e];
						if (data !== undefined) {
							priv.values[e] = new EntityValue(entity, owner.getAttribute(e), data);
						}
						const valx = priv.values[e];
						if (valx != null) {
							valx.touched = false;
						}
					});
					priv.key = rawDef.__KEY;
					priv.stamp = rawDef.__STAMP;

					const sel = entity._private.ownerSel;
					const pos = entity._private.posInSel;
					if (sel != null && pos != null) {
						sel.SetCachedEntityRawDef(pos, rawDef);

					}

				}
				resolve(entity);
			}).catch(reject);
		});
	}

	touchedAttributes() {
		const entity = this;
		const priv = entity._private;
		const vals = priv.values;
		const result = [];
		Object.keys(vals).forEach((e) => {
			const val = vals[e];
			if (val != null) {
				if (val.touched) {
					result.push(val.attribute.name);
				}
			}
		});
		return result;
	}

	touched() {
		return this.touchedAttributes().length !== 0;
	}
}


module.exports = {
	Entity,
	EntityProxy,
	EntityValue,
};
