/* eslint-disable prefer-destructuring */

// ----------------------

const { DataSource } = require('./datasource');
// const { dlog } = require('./tools');

class DataSourceOnEntity extends DataSource {
	constructor(namespace, id, type, dataclass, parentID, initialValue, catalog, properyInParent) {
		super(namespace, id, type);
		this.dataclassID = dataclass;
		this.parendID = parentID;
		this.catalog = catalog;
		this.dataclass = catalog.getDataClass(dataclass);
		if (parentID == null)
			this.parentSource = null;
		else {
			this.parentSource = DataSource.getSource(parentID, namespace);
			if (this.parentSource != null && this.dataclass == null) {
				this.dataclass = this.parentSource.dataclass;
			}
			if (this.parentSource != null && properyInParent != null) {
				if (this.parentSource.type === 'entity')
					this.properyInParent = properyInParent;
			}
		}
		this.entity = null;
		this.initialValue = initialValue;
	}

	clone(newid) {
		const clonedSource = new DataSourceOnEntity(this.namespace, newid, this.type, this.dataclassID, this.parendID, this.initialValue, this.catalog, this.properyInParent);
		return clonedSource;
	}

	getDataType(property) {
		const source = this;
		let result = null;
		if (source.dataclass != null) {
			const att = source.dataclass.getAttribute(property);
			if (att != null) {
				result = DataSource.mapDataType(att.type);
			}
		}
		return result;
	}

	async getValueAsParam(property, settings) {
		const source = this;
		return new Promise(function x9(resolve, reject) {
			source.getValue(property, settings).then((entity) => {
				if (entity == null) {
					resolve(null);
				} else {
					const result = { __ENTITY: true };
					entity.fillObjectWithModifiedValues(result);
					resolve(result);
				}

			}).catch(reject);

		});
	}


	async getValue(property /* , settings */) {
		const source = this;
		return new Promise(function x8(resolve, reject) {
			let canresolve = true;
			let result = null;
			if (source.entity != null) {
				if (property == null)
					result = source.entity;
				else {
					const att = source.dataclass.getAttribute(property);
					if (att != null) {
						const attvalue = source.entity[property];
						if (att.kind === 'storage' || att.kind === 'calculated') {
							result = attvalue;
						}
						else if (att.kind === 'relatedEntity' && attvalue != null) {
							canresolve = false;
							attvalue.fetch().then((relEntity) => {
								resolve(relEntity);
							}).catch((err) => {
								const errstack = err.errorStack;
								let errcode = -1;
								if (errstack != null && errstack.length > 0) {
									errcode = errstack[0].errCode;
								}
								if (errcode === 1542)
									resolve(null);
								else
									reject();
							});
						} else if (att.kind === 'relatedEntities' && attvalue != null) {
							result = attvalue;
						}
					}

				}
			}
			if (canresolve) {
				resolve(result);
			}
		});
	}

	recompute(doFireEvent = true) {
		const source = this;
		return new Promise(function x6(resolve, reject) {
			if (source.parentSource != null && source.parentSource.type === 'entity') {
				source.parentSource.getValue(source.properyInParent).then((resultEntity) => {
					source.entity = resultEntity;
					source.inited = true;
					if (doFireEvent)
						source.fireEvent('changed');
					resolve();
				}).catch(reject);
			}
			else if (source.parentSource != null && source.parentSource.type === 'entitysel') {
				source.parentSource.getValue(0).then((entity) => {
					source.entity = entity;
					source.inited = true;
					if (doFireEvent)
						source.fireEvent('changed');
					resolve();
				}).catch(reject);
			}
			else {
				const key = source.initialValue;
				if (source.dataclass != null && key != null && !source.inited) {
					source.dataclass.getEntity(key, { filterAttributes: source.filterAttributesText }).then((entity) => {
						source.entity = entity;
						source.inited = true;
						if (doFireEvent)
							source.fireEvent('changed');
						resolve();
					}).catch(reject);
				} else {
					source.inited = true;
					resolve();
				}
			}
		});
	}

	callAction(action) {
		const source = this;
		const { entity } = source;
		if (entity != null) {
			const sel = entity.getSelection();
			let pos = entity.getPos();
			if (action === 'save') {
				entity.save().then((savedEntity) => {
					source.entity = savedEntity;
					source.recomputeChildren().then(() => {
						source.fireEvent('changed');
					});
				});
			} else if (sel != null && pos !== -1) {
				sel.getLength().then((len) => {
					let newpos = -1;
					if (action === 'first') {
						if (len > 0)
							newpos = 0;
					}
					else if (action === 'last')
						newpos = len - 1;
					else if (action === 'next') {
						newpos = pos + 1;
						if (newpos >= len)
							newpos = len - 1;
					}
					else if (action === 'previous') {
						newpos = pos - 1;
						if (newpos < 0 && len > 0)
							pos = 0;

					}
					if (pos !== newpos && newpos >= 0) {
						sel.getEntity(newpos, { filterAttributes: source.filterAttributesText }).then((newentity) => {
							source.entity = newentity;
							source.recomputeChildren().then(() => {
								source.fireEvent('changed');
							});
						});
					}
				});
			}
		}
	}


	async setValue(property, value, doFireEvent = true) {
		const source = this;
		if (property == null) {
			source.entity = value;
		} else if (source.entity != null) {
			source.entity[property] = value;
		}

		await source.recomputeChildren(doFireEvent);
		if (doFireEvent)
			source.fireEvent('changed');

		/*
		source.recomputeChildren(doFireEvent).then(() => {
			if (doFireEvent)
				source.fireEvent('changed');
		});
		*/

		return null;
	}

	async setValueFromMethodResult(resp, doFireEvent = true) {
		if (this.dataclass != null) {
			if (this.dataclass.getName() === resp.__DATACLASS) {
				// eslint-disable-next-line
				const { Entity } = require('./entity');
				let ownerSel = null;
				let posInSel = null;
				if (this.entity != null && this.entity.getKey() === resp.__KEY) {
					ownerSel = this.entity.getSelection();
					posInSel = this.entity.getPos();
				}
				if (ownerSel != null && posInSel != null) {
					ownerSel.SetCachedEntityRawDef(posInSel, resp);
				}
				const entity = new Entity(this.dataclass, resp, null, ownerSel, posInSel);
				await this.setValue(null, entity.buildProxy(), doFireEvent);

			} else {
				await this.setValue(null, null, doFireEvent);
			}
		}
		return null;
	}


	buildCallFuncURL() {
		const source = this;
		let url = '';
		const entity = source.entity;
		let keypart = '___';
		if (entity != null)
			keypart = entity.getKey();
		if (keypart == null)
			keypart = '___';
		url = `${entity.getDataClass().getName() }(${keypart})`;
		return url;
	}


	getEntity() {
		return this.entity;
	}
}

module.exports = {
	DataSourceOnEntity,
};
