/* eslint-disable no-async-promise-executor */
/* eslint-disable no-else-return */
/* eslint-disable no-lonely-if */
// ------------------------------------------------------------- Methods
const { restRequest } = require('./rest');
// const { DataSource } = require('./datasource');

class Method4D {
	constructor(methodName, resultSource, paramSources, ownerSource = null) {
		this.methodName = methodName;
		this.resultSource = resultSource;
		if (paramSources == null)
			paramSources = [];
		this.paramSources = paramSources;
		this.ownerSource = ownerSource; // if the method is a member function of the source (entity, dataclass, ...)
	}

	isMemberFunction() {
		return this.ownerSource != null;
	}

	callServerSide(widget, callerType) {
		const method = this;

		return new Promise(async (resolve, reject) => {
			const { resultSource } = method;
			const { paramSources } = method;
			const { ownerSource } = method;

			let widgetid = null;
			// let formHandler = null;
			if (widget != null) {
				if (widget.serverSideRef != null) {
					// formHandler = widget;
					widgetid = widget.serverSideRef;
				} else {
					if (widget.id != null)
						widgetid = widget.id;
				}
			}
			let url = `/rest/$call/${method.methodName}?$method=entityset&$top=100`;
			if (method.isMemberFunction()) {
				url = `/rest/${ownerSource.buildCallFuncURL()}/${method.methodName}?$method=entityset&$top=100`;
			}
			if (resultSource != null && resultSource.filterAttributesText != null && resultSource.filterAttributesText !== '')
				url += `&$attributes=${resultSource.filterAttributesText}`;
			// const params = [{ caller: widgetid, eventType: callerType }];
			const params = [];
			const webEvent = { caller: widgetid, eventType: callerType };
			const promises = [];

			paramSources.forEach((source) => {
				if (source == null)
					promises.push(new Promise((cb) => {
						cb(null);
					}));
				else
					promises.push(source.getValueAsParam(null));
			});

			if (method.isMemberFunction()) {
				promises.push(ownerSource.getValueAsParam());
			}

			Promise.all(promises).then((paramValues) => {
				paramValues.forEach((paramValue) => {
					params.push(paramValue);
				});
				let body = '';
				// eslint-disable-next-line
				const { DataSource } = require('./datasource');
				const storageSource = DataSource.getSource('store');

				const webFormRef = window.$$componentRefs.getWebFormUniqID();
				const bodyobj = { params, webEvent, webFormRef };
				if (storageSource != null) {
					bodyobj.webSources = storageSource;
				}
				body = JSON.stringify(bodyobj);

				restRequest('POST', url, body, null).then((resp) => {
					if (resp != null && resp instanceof Object) {
						if (resp.__WEBSOURCES != null) {
							// eslint-disable-next-line
							const { DataSourceOnServerStorage } = require('./datasourceStorage')
							DataSourceOnServerStorage.updateServerStorageSources(resp.__WEBSOURCES);
						}

						// handle webform actions
						const webform = resp.__WEBFORM;
						if (webform != null)
							window.WebFormHandler.handleFormModifications(webform);

						if (resultSource != null) {
							resultSource.setValueFromMethodResult(resp);
							/*
							if (resp.__ENTITIES != null) {
								// eslint-disable-next-line
								const { EntitySelection } = require('./entitysel');
								const sel = new EntitySelection(resultSource.dataclass, { filterAttributes: resultSource.filterAttributesText }, resp);
								resultSource.setValue(null, sel);
							} else if (resp.__DATACLASS != null) {

							} else {
								const { result } = resp;
								resultSource.setValue(null, result);
							}
							*/
						}
						resolve(webform);
					} else {
						resolve();
					}
				}).catch(reject);

			});

		});

	}

	static buildMethod(component, associatedData, ownerSource = null) {
		let resultMethod = null;
		const { resultingDataSource, parameters } = component;
		let { methodName } = component;
		if (methodName == null) {
			ownerSource = null;
		} else {
			const parts = methodName.split('.');
			if (parts.length > 1) {
				methodName = parts.pop();
				const sourcename = parts.join('.');
				if (sourcename !== '') {
					ownerSource = DataSource.getSourceOrBuild(sourcename, null, window.$$datastores.ds, associatedData.namespace);
				}
				if (ownerSource == null) {
					console.warn(`missing source owner for method ${methodName}`);
					methodName = null;
				}
			} else {
				ownerSource = null;
			}
		}
		/*
		if (methodName != null && methodName.startsWith('.')) {
			methodName = methodName.substr(1);
			if (ownerSource == null) {
				// this a problem, we cannot have a member funtion without an owner object
				console.warn(`missing source owner for method ${methodName}`);
				methodName = null;
			}
		} else {
			ownerSource = null;
		}
		*/
		if (methodName != null && methodName !== '') {
			let params = [];
			if (parameters != null && Array.isArray(parameters))
				params = parameters.map(obj => DataSource.getSourceOrBuild(obj.name, null, window.$$datastores.ds, associatedData.namespace));
			resultMethod = new Method4D(methodName, DataSource.getSourceOrBuild(resultingDataSource, null, window.$$datastores.ds, associatedData.namespace), params, ownerSource);
		}
		return resultMethod;
	}

	static buildAction(component /* , associatedData */) {
		let resultAction = null;
		const { action } = component;
		if (action != null && action !== '' && action !== 'none') {
			resultAction = action;
		}
		return resultAction;
	}

	static validEvent(event) {
		return Method4D.acceptedEvents[event] === true;
	}

	static buildEvents(component /* , associatedData */) {
		const eventsResult = {};
		const events = component.eventType;
		if (events != null && typeof events === 'string') {
			events.split(',').forEach(event => {
				if (Method4D.validEvent(event)) {
					eventsResult[event] = true;
				}
			});
		}
		return eventsResult;
	}

	static replaceDataSources(component, elemSource, newid) {
		const { parameters } = component;
		DataSource.replaceDataSource(component, 'resultingDataSource', elemSource, newid);

		if (parameters != null && Array.isArray(parameters)) {
			parameters.forEach(obj => {
				DataSource.replaceDataSource(obj, 'name', elemSource, newid);
			});
		}
	}
}

Method4D.acceptedEvents = {};
'change,click,key,action,input,source'.split(',').forEach(event => { Method4D.acceptedEvents[event] = true; });

module.exports = {
	Method4D,
};
