// ----------------------------- DataClass
const { raw } = require('file-loader');
const { EntityCache } = require('./cache');
const { Entity } = require('./entity');
const { AddToUri, EntitySelection } = require('./entitysel');
const { restRequest } = require('./rest');

class DataClass {
	constructor(rawDef, owner) {
		const dataclass = this;
		this._private = {
			datastore: owner,
			className: rawDef.className,
			attributes: {},
			collectionName: rawDef.collectionName,
			cache: new EntityCache(),
			keys: rawDef.key.map(k => k.name),
		};
		if (rawDef.attributes != null) {
			rawDef.attributes.forEach((attDef) => {
				dataclass[attDef.name] = attDef;
				dataclass._private.attributes[attDef.name] = attDef;
			});
		}

	}

	getName() {
		return this._private.className;
	}

	getCollectionName() {
		return this._private.collectionName;
	}

	getDataStore() {
		return this._private.datastore;
	}

	getAttribute(attname) {
		if (this[attname] !== undefined)
			return this._private.attributes[attname];
		return null;
	}

	getAllAttributes() {
		return this._private.attributes;
	}

	getEntity(primKey, settings = null) {
		const dataclass = this;
		const url = `/rest/${  dataclass.getName()  }(${  primKey  })`;
		return new Promise((resolve, reject) => {
			// var rawdef = dataclass.getCache().retainEntry(primKey, dataclass.getCacheDelay());
			const rawdef = null;
			if (rawdef != null) {
				const entity = new Entity(dataclass, rawdef, settings);
				resolve(entity.buildProxy());
			}
			else {
				let added = '';
				if (settings != null) {
					if (settings.expand != null) {
						let expand = null;
						if (typeof settings.expand === 'string') {
							expand = settings.expand;
						} else {
							expand = settings.expand.join(',');
						}
						added = AddToUri(added, `$expand=${  expand}`);
					}
				}
				restRequest('get', url + added).then((resp) => {
					const entity = new Entity(dataclass, resp, settings);
					resolve(entity.buildProxy());
				}).catch(reject);
			}
		});
	}

	query(queryStr, settings) {
		const newsettings = {};
		if (settings != null) {
			Object.keys(settings).forEach((e) => {
				newsettings[e] = settings[e];
			});
			/*
			if (settings.queryParams != null)
				newsettings.queryParams = settings.queryParams;
			*/
		}
		newsettings.queryStr = queryStr;
		const sel = new EntitySelection(this, newsettings, null);
		return sel;
	}

	async getDistinctValues(attName, maxElems = 30, queryFilter = null) {
		const dataclass = this;
		let url = `/rest/${dataclass.getName()}?$distinct=true&$attributes=${attName}`;
		if (maxElems != null) {
			url += `&$top=${maxElems}`;
		}
		if (queryFilter != null) {
			url += `&$filter="${queryFilter}"`;
		}
		return new Promise((resolve, reject) => {
			restRequest('get', url).then((resp) => {
				resolve(resp);
			}).catch(reject);
		});
	}

	allEntities(settings) {
		return this.query(null, settings);
	}

	newEmptySelection() {
		const sel = new EntitySelection(this, null, null);
		sel.initAsEmpty();
		return sel;
	}

	getCacheDelay() {
		return this.getDataStore().getCacheDelay();
	}

	getCache() {
		return this._private.cache;
	}

	getKeys() {
		return this._private.keys;
	}
}

module.exports = {
	DataClass,
};
