/* eslint-disable no-async-promise-executor */

// ----------------------------- EntitySelection

const { restRequest } = require('./rest');
const { Entity } = require('./entity');

function AddToUri(queryPart, str) {
	if (queryPart === '') {
		return `?${  str}`;
	}
	return `${queryPart  }&${  str}`;
}

class EntitySelection {
	constructor(owner, settings = null, rawDef = null) {
		const sel = this;
		this._private = {
			curPage: { first: 0, size: 0, entitiesDef: [] },
			selLength: 0,
			maxEntities: null,
			initialFirst: 0,
			pageLength: 100,
			inited: false,
			owner,
			fetching: false,
			settings,
			isRelatedSel: false,
			relatedSelURI: null,
			dataSetName: null,
			filterAttributes : null,
		};
		if (rawDef != null) {
			if (rawDef.__deferred != null) {
				this._private.relatedSelURI = rawDef.__deferred.uri;
				this._private.isRelatedSel = true;
			}
			else {
				this.managePage(rawDef);
				this._private.inited = true;
			}
		}
		if (settings != null) {
			if (settings.relatedSelURI != null) {
				this._private.isRelatedSel = true;
				this._private.relatedSelURI = settings.relatedSelURI;
			}
			else if (settings.queryStr != null) {
				this._private.queryStr = settings.queryStr;
				if (settings.queryParams != null)
					this._private.queryParams = settings.queryParams;
			}
			if (settings.first != null)
				this._private.initialFirst = settings.first;
			if (settings.pageLength != null)
				this._private.pageLength = settings.pageLength;
			if (settings.filterAttributes != null)
				sel._private.filterAttributes = settings.filterAttributes;
		}
	}

	initAsEmpty() {
		this._private.selLength = 0;
		this._private.inited = true;
		this._private.maxEntities = 0;
	}

	get length() { return this._private.selLength; }

	getLength(settings = this._private.settings) {
		const sel = this;
		return new Promise((resolve, reject) => {
			if (sel._private.inited) {
				resolve(sel._private.selLength);
			} else {
				sel.fetchPage(0, sel._private.pageLength, settings).then((rawDef) => {
					sel.managePage(rawDef);
					resolve(sel._private.selLength);
				}).catch(reject);
			}
		});
	}

	getMaxEntities() {
		return this._private.maxEntities; // not async, because is usually called after getLength
	}

	getDataClass() { return this._private.owner; }

	getDescriptorAsParam() {
		const result = { __ENTITIES:true };
		const priv = this._private;
		if (priv.owner != null)
			result.__DATACLASS = priv.owner.getName();
		if (priv.inited) {
			if (priv.dataSetName != null)
				result.__DATASET = priv.dataSetName;
		}
		return result;
	}

	getComputedURL(settings = this._private.settings, forFuncCall = false) {
		const sel = this;
		const priv = sel._private;

		let url = sel.getDataClass().getName();
		if (!forFuncCall)
			url = `/rest/${url}`;
		let added = '';
		if (priv.dataSetName == null) {
			if (priv.isRelatedSel) {
				url = priv.relatedSelURI;
				const parts = url.split('?');
				[url] = parts;
				if (parts.length > 1)
					added = AddToUri(added, parts[1]);
				added = AddToUri(added, '&$method=subentityset');

			} else {
				added = AddToUri(added, '$method=entityset');
				if (priv.queryStr != null) {
					added = AddToUri(added, `$filter='${priv.queryStr}'`);
					if (priv.queryParams != null) {
						let s = JSON.stringify(priv.queryParams);
						s = encodeURI(s);
						added = AddToUri(added, `$params='${s}'`);
					}
				}
			}
		}
		else {
			url = `${url}/$entityset/${priv.dataSetName}`;
		}

		let allowFilterAttributes = true;

		if (settings != null) {
			if (settings.expand != null) {
				let expand = null;
				if (typeof settings.expand === 'string') {
					expand = settings.expand;
				} else {
					expand = settings.expand.join(',');
				}
				added = AddToUri(added, `$expand=${expand}`);
			}

			if (settings.queryPlan) {
				added = AddToUri(added, '$queryplan=true');
			}
			if (settings.queryPath) {
				added = AddToUri(added, '$querypath=true');
			}
			if (settings.progressInfo != null) {
				added = AddToUri(added, `$progressWebInfo=${settings.progressInfo}`);
			}
			if (settings.maxEntities != null && settings.maxEntities === true) {
				added = AddToUri(added, '$maxEntities=true');
			}
			if (settings.forceReloadEntity)
				allowFilterAttributes = false;
		}

		if (priv.filterAttributes && allowFilterAttributes) {
			added = AddToUri(added, `$attributes=${sel._private.filterAttributes}`);
		}

		return { url, added };
	}

	fetchPage(first, size, settings = this._private.settings) {
		const sel = this;
		sel._private.fetching = true;
		return new Promise((resolve, reject) => {

			// eslint-disable-next-line prefer-const
			let { url, added } = sel.getComputedURL(settings);

			added = AddToUri(added, `$skip=${first}`);
			added = AddToUri(added, `$top=${size}`);

			restRequest('get', url + added).then((resp) => {
				sel._private.fetching = false;
				resolve(resp);
			}).catch((error) => { reject(error); sel._private.fetching = false; });
		});
	}

	managePage(rawDef, nochangeInCurpage = false) {
		const {curPage} = this._private;
		if (!this._private.inited) {
			this._private.inited = true;
			if (rawDef.__MAXENTITIES != null)
				this._private.maxEntities = rawDef.__MAXENTITIES;
			this._private.selLength = rawDef.__COUNT;
			if (rawDef.__queryPlan != null)
				this.queryPlan = rawDef.__queryPlan;
			if (rawDef.__extendedQueryPlan != null)
				this.extendedQueryPlan = rawDef.__extendedQueryPlan;
			if (rawDef.__extendedQueryPath != null)
				this.extendedQueryPath = rawDef.__extendedQueryPath;
			if (rawDef.__queryPath != null)
				this.queryPath = rawDef.__queryPath;
			if (rawDef.__ENTITYSET != null) {
				const setname = rawDef.__ENTITYSET.split('/');
				const lastpart = setname[setname.length - 1];
				if (lastpart.split('?').length > 1) {
					this._private.isRelatedSel = true;
					this._private.relatedSelURI = rawDef.__ENTITYSET;
				}
				else
					this._private.dataSetName = lastpart;
			}
		}

		if (nochangeInCurpage) {
			return rawDef.__ENTITIES;
		}
		curPage.first = rawDef.__FIRST;
		curPage.size = rawDef.__SENT;
		curPage.entitiesDef = rawDef.__ENTITIES;
		const dataclass = this.getDataClass();
		dataclass.getCache().makeRoomFor(curPage.size);

		return null;
	}


	async getCollection(index, howmany, attributesStr, settings = this._private.settings) {
		const sel = this;
		let howmanyCopy = howmany;

		const attributes = {};

		function subFetchCollection(sel1, resolve, reject) {
			let needFetch = false;
			const {curPage} = sel1._private;
			if (sel1._private.inited) {
				if (howmanyCopy == null)
					howmanyCopy = sel1._private.selLength - index;
				else if (howmanyCopy > sel1._private.selLength - index)
					howmanyCopy = sel1._private.selLength - index;

				if (index >= sel1._private.selLength || howmanyCopy <= 0)
					needFetch = false;
				else if (index >= curPage.first && index+howmanyCopy <= (curPage.first + curPage.size)) {
					needFetch = false;
				} else {
					needFetch = true;
				}

			} else {
				needFetch = true;
			}

			function subresolve() {
				const result = [];
				for (let i = index, nb = index + howmanyCopy; i < nb; i += 1) {
					const trueEntity = new Entity(sel1.getDataClass(), curPage.entitiesDef[i - curPage.first], settings, sel1, i);
					const entity = trueEntity.buildProxy();
					const elem = {};
					Object.keys(attributes).forEach((e) => {
						const parts = e.split('.');
						if (parts.length === 1) {
							const val = entity[e];
							elem[e] = val;
						} else {
							let curroot = parts[0];
							let val = entity[curroot];
							const nbparts = parts.length;
							let curpart = 1;
							while (val != null && curpart < nbparts) {
								curroot = parts[curpart];
								if (typeof val === 'object') {
									val = val[curroot];
								} else {
									val = null;
								}
								curpart++;
							}
							elem[e] = val;
						}
					});
					result.push(elem);
				}
				resolve(result);
			}

			if (needFetch) {
				if (sel1._private.fetching) {
					// console.log("waiting with index :"+index);
					setTimeout(() => { subFetchCollection(sel1, resolve, reject); }, 0);
				}
				else {
					sel1.fetchPage(index, Math.max(sel1._private.pageLength, howmanyCopy == null ? 100 : howmanyCopy), settings).then((rawDef) => {
						sel1.managePage(rawDef);
						if (howmanyCopy == null)
							howmanyCopy = sel1._private.selLength - index;
						else if (howmanyCopy > sel1._private.selLength - index)
							howmanyCopy = sel1._private.selLength - index;
						if (index >= sel1._private.selLength)
							resolve([]);
						else {
							subresolve();
						}
					}).catch(reject);
				}
			} else if (index >= sel1._private.selLength)
				resolve(null);
			else {
				subresolve();
			}
		}

		if (attributesStr == null || attributesStr === '') {
			return new Promise((resolve) => {
				resolve([]);
			});
		}

		const atts = attributesStr.split(',');
		atts.forEach((elem) => {
			attributes[elem] = true;
		});
		return new Promise((resolve, reject) => {
			subFetchCollection(sel, resolve, reject);
		});


	}

	async getEntity(index, settings) {
		const sel = this;
		if (settings === undefined)
			settings = sel._private.settings;

		function subFetch(sel1, resolve, reject) {
			let needFetch = false;
			const {curPage} = sel1._private;
			if (sel1._private.inited) {
				if (index >= sel1._private.selLength)
					needFetch = false;
				else if (index >= curPage.first && index < (curPage.first + curPage.size)) {
					needFetch = (settings != null && settings.forceReloadEntity);
				} else {
					needFetch = true;
				}

			} else {
				needFetch = true;
			}

			if (needFetch) {
				if (sel1._private.fetching) {
					// console.log("waiting with index :"+index);
					setTimeout(() => { subFetch(sel1, resolve, reject); }, 0);
				}
				else if (settings != null && settings.forceReloadEntity) {
					sel1.fetchPage(index, 1, settings).then((rawDef) => {
						const entitiesDef = sel1.managePage(rawDef, true);
						const entity = new Entity(sel1.getDataClass(), entitiesDef[0], settings, sel1, index);
						resolve(entity.buildProxy());
					}).catch(reject);
				} else {
					sel1.fetchPage(index, sel1._private.pageLength, settings).then((rawDef) => {
						sel1.managePage(rawDef);
						if (index >= sel1._private.selLength)
							resolve(null);
						else {
							const entity = new Entity(sel1.getDataClass(), curPage.entitiesDef[index - curPage.first], settings, sel1, index);
							resolve(entity.buildProxy());
						}
					}).catch(reject);
				}
			} else if (index >= sel1._private.selLength)
				resolve(null);
			else {
				const entity = new Entity(sel1.getDataClass(), curPage.entitiesDef[index - curPage.first], settings, sel1, index);
				resolve(entity.buildProxy());
			}
		}

		return new Promise((resolve, reject) => {
			subFetch(sel, resolve, reject);
		});
	}

	async orderBy(attributes, settings = null) {
		const sel = this;
		return new Promise(async function SelOrderBy(resolve, reject) {
			if (sel._private.inited && sel._private.selLength === 0) {
				resolve(sel);
			} else {
				// eslint-disable-next-line prefer-const
				let { url, added } = sel.getComputedURL();
				added = AddToUri(added, `$orderby=${attributes}`);
				added = AddToUri(added, '$method=entityset');
				if (settings != null && settings.progressInfo != null)
					added = AddToUri(added, `$progressWebInfo=${settings.progressInfo}`);

				try {
					const resp = await restRequest('get', url + added);
					const newsel = new EntitySelection(sel.getDataClass(), { filterAttributes: sel._private.filterAttributes }, resp);
					resolve(newsel);
				}
				catch (err) {
					reject(err);
				}
			}
		});
	}

	SetCachedEntityRawDef(pos, rawDef) {
		const sel = this;
		const {curPage} = sel._private;
		if (pos >= curPage.first && pos < (curPage.first + curPage.size)) {
			curPage.entitiesDef[pos - curPage.first] = rawDef;
		}
	}

	setFilteredAttributes(filterAttributeText) {
		const priv = this._private;
		priv.filterAttributes = filterAttributeText;
		if (priv.settings != null)
			priv.settings.filterAttributes = filterAttributeText;
		priv.curPage = { first: 0, size: 0, entitiesDef: [] };
		this.getDataClass().getCache().clearAll();
	}

	destroy() {
		const sel = this;
		const priv = sel._private;
		if (priv.inited) {
			if (priv.dataSetName != null && priv.dataSetName !== '') {
				const url = '/rest/$entityset/$release';
				const body = JSON.stringify([priv.dataSetName]);
				restRequest('post', url, body);
			}

		}
	}

}

module.exports = {
	AddToUri,
	EntitySelection,
};
