
// ----------------------

const { DataSource } = require('./datasource');
// const { dlog } = require('./tools');

class DataSourceOnDataClass extends DataSource {
	constructor(namespace, id, dataclass, catalog, parentID, properyInParent) {
		super(namespace, id, 'dataclass');
		this.catalog = catalog;
		this.dataclass = dataclass
		if (parentID == null)
			this.parentSource = null;
		else {
			this.parentSource = DataSource.getSource(parentID, namespace);
			if (this.parentSource != null && properyInParent != null) {
				this.properyInParent = properyInParent;
			}
		}
	}

	clone(newid) {
		const clonedSource = new DataSourceOnDataClass(this.namespace, newid, this.dataclass, this.catalog, this.parent.id, this.dataclass.getName());
		return clonedSource;
	}

	buildCallFuncURL() {
		return this.dataclass.getName();
	}
}


module.exports = {
	DataSourceOnDataClass,
};
