const { DataSourceOnServerStorage } = require('./datasourceStorage');
const { DataSourceOnScalar } = require('./datasourceScalar');
const { DataSourceOnEntity } = require('./datasourceEntity');
const { DataSourceOnEntitySel } = require('./datasourceEntitysel');
const { Method4D } = require('./method');


class DataSourceManager {
	constructor() {
		this.spaces = { '__default': { sources: {} } };
		this.dataTypeMap = {};
		this.globalStamp = 1;
		this.anonymCount = 1;

		this.dataTypeMap = {
			'bool': 'bool',
			'string': 'string',
			'text': 'string',
			'uuid': 'string',
			'short': 'number',
			'long': 'number',
			'number': 'number',
			'long64': 'number',
			'duration': 'number',
			'object': 'object',
			'date': 'date',
		};

	}


	getOrBuildNameSpace(namespace) {
		if (this.spaces[namespace] == null) {
			this.spaces[namespace] = { sources: {} };
		}
		return this.spaces[namespace];
	}

	getNameSpace(namespace) {
		return this.spaces[namespace];
	}

	buildSource(sourcestr, initialValue, ds, namespace = '__default') {
		const space = this.getOrBuildNameSpace(namespace);
		if (sourcestr == null)
			return;

		const multiSources = sourcestr.split(',');
		let sourcestrCopy = sourcestr;
		if (multiSources.length > 1) {
			multiSources.forEach((oneSourceStr) => {
				this.buildSource(oneSourceStr, null, ds);
			});
		}
		else if (sourcestrCopy !== '') {
			let curparent = null;
			let dataType = null;
			let dataTypestr = null;
			let dataclassOrSel = null;
			[sourcestrCopy, dataTypestr, dataclassOrSel] = sourcestrCopy.split(':');
			if (dataTypestr != null)
				dataType = this.mapDataType(dataTypestr);

			if (space.sources[sourcestrCopy] == null) // if does not exists yet
			{
				const parts = sourcestrCopy.split('.');
				if (parts.length > 1) {
					let sname = '';
					const nbparent = parts.length;
					for (let i = 0; i < nbparent; i += 1) {
						if (i === 0) {
							sname = parts[i];
							let psource = space.sources[sname];
							if (psource == null && sname === 'store') {
								psource = new DataSourceOnServerStorage(namespace, sname, 'serverStorage', ds, dataType);
								space.sources[sname] = psource;
							}
							if (psource == null) {
								console.error(`base source is missing to build:${sourcestrCopy}`);
								break;
							}
							else {
								curparent = psource;
							}
						} else {
							sname += `.${parts[i]}`;
							const psource = space.sources[sname];
							if (psource != null) {
								curparent = psource;
							} else if (curparent.type === 'entity') {
								const { dataclass } = curparent;
								if (dataclass == null) {
									console.error(`dataclass is missing to build:${sourcestrCopy}`);
									break;
								}
								else {
									const att = dataclass.getAttribute(parts[i]);
									if (att == null) {
										console.error(`attribute:${parts[i]} not found to build:${sourcestrCopy}`);
										break;
									} else if (att.kind === 'storage' || att.kind === 'calculated' || att.kind === 'alias') {
										const source = new DataSourceOnScalar(namespace, sname, 'scalar', parts[i], curparent);
										space.sources[sname] = source;
										curparent = source;
									} else if (att.kind === 'relatedEntity') {
										const relDataClass = ds.getDataClass(att.type);
										const source = new DataSourceOnEntity(namespace, sname, 'entity', relDataClass.getName(), curparent.id, null, ds, parts[i]);
										space.sources[sname] = source;
										curparent = source;
									} else if (att.kind === 'relatedEntities') {
										const relDataClass = ds.getDataClassByCollectionName(att.type);
										const source = new DataSourceOnEntitySel(namespace, sname, 'entitysel', relDataClass.getName(), curparent.id, null, ds, parts[i]);
										space.sources[sname] = source;
										curparent = source;
									}
								}
							}
							else if (curparent.type === 'entitysel' && parts[i] === '[]') {
								const source = new DataSourceOnEntity(namespace, sname, 'entity', curparent.dataclass.getName(), curparent.id, null, ds);
								space.sources[sname] = source;
								curparent = source;
							}
							else {
								const source = new DataSourceOnScalar(namespace, sname, 'scalar', parts[i], curparent, null, dataType);
								space.sources[sname] = source;
								curparent = source;
							}
						}
					}
				} else if (sourcestrCopy === 'store') {
					const source = new DataSourceOnServerStorage(namespace, sourcestrCopy, 'serverStorage', ds, dataType);
					space.sources[sourcestrCopy] = source;
					curparent = source;
				} else if (dataTypestr === 'entitysel') {
					if (dataclassOrSel != null) {
						const source = new DataSourceOnEntitySel(namespace, sourcestrCopy, dataTypestr, dataclassOrSel, null, 'all', ds);
						space.sources[sourcestrCopy] = source;
						curparent = source;
					}
				} else if (dataTypestr === 'entity') {
					if (dataclassOrSel != null) {
						const dsel = this.getSource(dataclassOrSel, namespace);
						if (dsel != null && dsel.type === 'entitysel' && dsel.dataclass != null) {
							const dataclassName = dsel.dataclass.getName();
							const source = new DataSourceOnEntity(namespace, sourcestrCopy, dataTypestr, dataclassName, dataclassOrSel, 'first', ds);
							space.sources[sourcestrCopy] = source;
							curparent = source;
						}
					}
				}
				else {
					// simple datasource
					const source = new DataSourceOnScalar(namespace, sourcestrCopy, 'scalar', null, null, null, dataType);
					space.sources[sourcestrCopy] = source;
					curparent = source;
				}
			}
		}

		const ev = new Event('datasource:created');
		ev.id = sourcestr;
		window.dispatchEvent(ev);
	}

	create({ id, type, dataclass, initialValue, from, dataType }, ds, namespace = '__default') {
		let datasource = this.getSource(id, namespace);
		if (datasource == null) {
			let pid = null;
			if (from != null && from !== '')
				pid = from;

			switch (type) {
				case 'entity':
					datasource = new DataSourceOnEntity(namespace, id, type, dataclass, pid, initialValue, ds);
					break;
				case 'entitysel':
					datasource = new DataSourceOnEntitySel(namespace, id, type, dataclass, pid, initialValue, ds);
					break;
				case 'auto':
					datasource = this.getSourceOrBuild(from, initialValue, ds, namespace);
					this.AddAlias(id, datasource, namespace);
					break;
				case 'scalar':
					datasource = new DataSourceOnScalar(namespace, id, 'scalar', null, null, initialValue, dataType);
					break;
				case 'array':
					datasource = new DataSourceOnScalar(id, 'scalar', null, null, initialValue, 'array');
					break;
				case 'object':
					datasource = new DataSourceOnScalar(namespace, id, 'scalar', null, null, initialValue, 'object');
					break;
				default:
					break;
			}

			const space = this.getOrBuildNameSpace(namespace);

			if (datasource != null) {
				space.sources[datasource.id] = datasource;
			}
		}
		return datasource;
	}



	getSource(id, namespace = '__default') {
		const space = this.getNameSpace(namespace);
		if (space == null)
			return null;
		return space.sources[id];
	}

	getSourceOrBuild(id, initialValue, ds, namespace = '__default') {
		if (id == null)
			return null;
		this.buildSource(id, initialValue, ds, namespace);
		const [trueID] = id.split(':');
		return this.getSource(trueID, namespace);
	}

	AddAlias(id, datasource, namespace = '__default') {
		if (id != null && id !== '') {
			this.getOrBuildNameSpace(namespace).sources[id] = datasource;
		}
	}

	replaceDataSource(obj, prop, elemSource, newid) {
		const original = obj[`${prop  }-original`];
		if (original == null)
			obj[`${prop  }-original`] = obj[prop];
		else
			obj[prop] = obj[`${prop  }-original`];

		const existingOne = obj[prop];
		if (existingOne != null && existingOne !== '')
			obj[prop] = this.changePath(existingOne, elemSource, newid);
	}

	// eslint-disable-next-line class-methods-use-this
	changePath(existingPath, elemSource, newid) {
		let result = existingPath;
		if (existingPath != null && elemSource != null && newid != null && existingPath.startsWith(elemSource)) {
			result = newid + existingPath.substring(elemSource.length);
		}
		return result;
	}

	getSources(sourceList, namespace = '__default') {
		const result = [];
		const list = sourceList.split(',');
		list.forEach((elem) => {
			const sourcestr = elem.split(':')[0];
			const source = this.getSource(sourcestr, namespace);
			result.push(source); // can be null
		});
		return result;
	}

	getAllSources(namespace = '__default') {
		const space = this.getNameSpace(namespace);
		if (space == null)
			return null;
		return space.sources;
	}

	mapDataType(datatype) {
		const result = this.dataTypeMap[datatype];
		return result;
	}

	buildDependencies(namespace = '__default') {
		const space = this.getOrBuildNameSpace(namespace);

		const datasourcesNames = Object.keys(space.sources);
		const root = space.root || new this(namespace, '$$root', 'root');

		datasourcesNames.forEach((e) => {
			const source = space.sources[e];

			if (!source) {
				return;
			}

			if (!source.parentSource) {
				root.children[source.id] = source;
			}
			else {
				source.parentSource.children[source.id] = source;
			}
		});

		space.root = root;
	}


	createRequestOptimization(namespace = '__default') {
		const space = this.getNameSpace(namespace);
		if (space != null)
			space.root.createRequestOptimization();
	}

	dumpTree(namespace = '__default') {
		const space = this.getNameSpace(namespace);
		if (space == null)
			return '';
		return space.root.dumpTree('', 0);
	}


	computeInitialValues(namespace = '__default') {
		const space = this.getNameSpace(namespace);
		if (space != null)
			space.root.recomputeChildren();
	}


	initServerStorage() {
		return new Promise((resolve) => {
			const source = this.getSource('store');
			if (source == null) {
				resolve();
			} else {
				const method = new Method4D('$$', null, null);
				method.callServerSide(null, null).then(() => {
					resolve();
				});
			}
		});
	}

	// eslint-disable-next-line class-methods-use-this
	getFormNameSpace(formioElement) {
		let namespace = formioElement.root.element.getAttribute('data-namespace');
		if (namespace == null || namespace === '')
			namespace = '__default';
		return namespace;
	}


}

module.exports = {
	DataSourceManager,
};
