const { DataSource } = require('./datasource');

class DataSourceOnServerStorage extends DataSource {
	constructor(namespace, id, type, catalog, dataType) {
		super(namespace, id, type);
		if (dataType == null) {
			dataType = 'object';
		}
		this.dataType = dataType;
		this.catalog = catalog;
		this.value = null;
		this.fromServerStorage = true;
	}

	getValue(property /* , settings */) {
		const source = this;
		return new Promise((resolve) => {
			if (property == null)
				resolve(source.value);
			else if (source.value == null || !(source.value instanceof Object))
				resolve(null);
			else
				resolve(source.value[property]);
		});
	}


	async setValue(property, value, doFireEvent = true) {
		const source = this;
		if (property == null) {
			source.value = value;
			source.touch();
		}
		else {
			if (source.value == null || !(source.value instanceof Object)) {
				source.value = {};
			}
			source.value[property] = value;
			source.touch();
		}

		await source.recomputeChildren(doFireEvent);
		if (doFireEvent)
			source.fireEvent('changed');

		/*
		if (!doNotRecompute) {
			source.recomputeChildren(doNotUpdateWidgets).then(() => {
				if (!doNotUpdateWidgets)
					source.updateWidgets();
			});
		}
		*/
		return null;
	}

	getFullServerStorage() {
		return this.value;
	}
}

DataSourceOnServerStorage.updateServerStorageSources = function updateServerStorageSources(rawData) {
	const datasource = DataSource.getSource('store');
	if (datasource != null) {
		if (rawData == null) {
			datasource.setValue(null, null);
			datasource.freezeStamp(true);
		} else {
			Object.keys(rawData).forEach((e) => {
				const subsource = DataSource.getSource(`store.${e}`);
				if (subsource != null) {
					const value = rawData[e];
					subsource.setValue(null, value);
					subsource.freezeStamp(true);
				}
			});
		}
	}
};

module.exports = {
	DataSourceOnServerStorage,
};
