// ------------------------------------------------------------- init

const { DataSource } = require('./datasource');
const { DataSourceManager } = require('./datasourceManager');
const { DataStore } = require('./datastore');
const { Method4D } = require('./method');
const { Utils } = require('./tools');
const { WebFormHandler } = require('./WebFormHandler');

function fireEvent(name, data, err = null) {
	const ev = new window.Event(name);
	ev.data = data;
	if (err != null)
		ev.caughtErr = err;
	window.dispatchEvent(ev);
}

/**
 * Initialize the data store
 */
export async function createDataStore() {
	DataSource.init();
	// try {
	const ds = await new DataStore().fetch();
	window.$$datastores.ds = ds;
	fireEvent('bundle:ds:created', ds);
	return ds;

	/*
	} catch(e) {
		console.error('cannot init ds', e);
	}
	return null;
	*/

}

/**
 * Create global datasources
 */
export function createDataSources(list = [], ds, namespace = '__default') {
	const result = list.forEach(one => DataSource.create(one, ds, namespace));

	fireEvent('bundle:sources:meta', result);
	return result;
}

async function initFramework() {
	try {
		const ds = await createDataStore();

		if (ds != null) {
			const sources = Array.prototype.map.call(document.getElementsByClassName('datasource'), (elem) => {
				const id = elem.getAttribute('data-source');
				const type = elem.getAttribute('data-type');
				const pid = elem.getAttribute('data-parent');
				const dataclass = elem.getAttribute('data-dataclass');
				const initialValue = elem.getAttribute('data-initialValue');

				return { id, type, pid, dataclass, initialValue };
			});

			createDataSources(sources, ds);
			DataSource.buildDependencies();
			DataSource.createRequestOptimization();

			try {
				await DataSource.computeInitialValues();
				await DataSource.initServerStorage();
				// Widget.updateAllContents();
			} catch (e) {
				console.error('cannot set initial values for DataSources', e);
			}

			fireEvent('bundle:ready', ds);
		} else
			fireEvent('bundle:ready', null, 'unknown error');
	}
	catch (err) {
		fireEvent('bundle:ready', null, err);
	}
}

function onload() {
	initFramework();
}

class ComponentRef {

	constructor() {
		this.refs = {};
		this.webFormRef = String(Math.round(Math.random() * 1000000) * new Date().getMilliseconds());
	}

	buildComponentRef(componentAssociatedData, component) {
		const { id } = componentAssociatedData;
		componentAssociatedData.owner = component;
		this.refs[id] = componentAssociatedData;
		return `data-compRef="${id}"`;
	}

	getComponent(id) {
		return this.refs[id];
	}

	getWebFormUniqID() {
		return this.webFormRef;
	}
}

window.addEventListener('load', onload);
window.$$datastores = {};
window.DataSource = DataSource;
window.DataSourceManager = DataSourceManager;
window.Method4D = Method4D;
window.WebFormHandler = WebFormHandler;
window.Utils = Utils;
window.$$componentRefs = new ComponentRef();

// some debug code, to be removed
window.allowedFilter = { computeSource: false, updateWidget: false };
window.debugIsOn = true;
