/* eslint-disable no-plusplus */
/* eslint-disable prefer-destructuring */
/* eslint-disable prefer-template */
/* eslint-disable no-restricted-syntax */
/* eslint-disable guard-for-in */

class WebFormHandler {
	constructor(formIOcomponent, componentAssociatedData) {
		this.compID = componentAssociatedData.id;
		this.serverSideRef = formIOcomponent.component.serverSideRef;
		this.component = formIOcomponent;
		this.associatedData = componentAssociatedData;
	}

	callVerb(verb, actionParam) {
		const component = this.component;
		const funcname = verb + 'Handler';
		const func = component[funcname];
		if (func != null) {
			func.call(component, this.associatedData, actionParam.value);
		}
	}

	static associateHandler(formIOcomponent, componentAssociatedData) {
		if (componentAssociatedData != null) {
			const handler = new WebFormHandler(formIOcomponent, componentAssociatedData);
			componentAssociatedData.formHandler = handler;
			WebFormHandler.handlerMapByID[componentAssociatedData.id] = handler;
			const ref = handler.serverSideRef;
			if (ref != null) {
				WebFormHandler.handlerMapByRef[ref] = handler;
			}
			componentAssociatedData.namespace = DataSource.getFormNameSpace(formIOcomponent);
		}
	}

	static getHandler(compRef) {
		let handler = WebFormHandler.handlerMapByRef[compRef];
		if (handler === undefined) handler = null;
		return handler;
	}

	static handleFormModifications(webform) {
		for (const compRef in webform) {
			const handler = WebFormHandler.getHandler(compRef);
			if (handler != null) {
				const actions = webform[compRef];
				for (const verb in actions) {
					const actionParam = actions[verb];
					handler.callVerb(verb, actionParam);
				}
			}
		}
	}

	static generateUID() {
		let result = '';
		for (let i = 0; i < 10; ++i) {
			result += String.fromCharCode(((Math.random() * 1000) % 26) + 97);
		}
		return '4d-' + result;
	}
}

WebFormHandler.handlerMapByID = {};
WebFormHandler.handlerMapByRef = {};

module.exports = {
	WebFormHandler,
};
