/* eslint-disable prefer-destructuring */
/* eslint-disable no-else-return */
// ----------------------

const { Utils } = require('./tools');
const { DataSource } = require('./datasource');

class DataSourceOnScalar extends DataSource {
	constructor(namespace, id, type, properyInParent, parentSource, initialValue, dataType) {
		super(namespace, id, type);
		this.parentSource = parentSource;
		this.initialValue = initialValue;
		this.properyInParent = properyInParent;
		if (!parentSource)
			this.value = null;
		else {
			this.fromServerStorage = parentSource.fromServerStorage;
			if (dataType == null)
				dataType = parentSource.getDataType(properyInParent);
		}
		if (dataType == null)
			dataType = 'string';
		this.dataType = dataType;

	}

	clone(newid) {
		const clonedSource = new DataSourceOnScalar(this.namespace, newid, this.type, this.properyInParent, this.parentSource, this.initialValue, this.dataType);
		return clonedSource;
	}

	getDataType(property) {
		if (property == null)
			return this.dataType;
		else if (this.value == null)
			return null;
		else if (Array.isArray(this.value))
			return 'array';
		else if (this.value instanceof Date)
			return 'date';
		else
			return typeof(this.value);
	}

	getValue(property, settings) {
		const source = this;
		return new Promise(function x2(resolve, reject) {
			if (!source.parentSource) {
				// resolve(source.convertAs(source.dataType, source.value));
				if (property == null)
					resolve(source.value);
				else if (source.value == null || !(source.value instanceof Object))
					resolve(null);
				else
					resolve(source.value[property]);
			}
			else {
				source.parentSource.getValue(source.properyInParent, settings).then(function r7(result) {
					if (property != null) {
						let res = null;
						if (result != null && result instanceof Object)
							res = result[property];
						resolve(res);
					}
					else {
						resolve(source.convertAs(source.dataType, result));
					}
				}).catch(reject);
			}

		});
	}

	async getCollection(index, howmany, propertyStr) {
		const source = this;
		return new Promise(function xcol5(resolve) {
			const value = source.value;
			if (Array.isArray(value)) {
				const propcol = propertyStr.split(',');
				const result = [];
				if (index < 0)
					index = 0;
				if (howmany + index > value.length)
					howmany = value.length - index;
				for (let i = index; i < howmany; ++i) {
					const obj = {};
					const val = value[i];
					const isobj = typeof val === 'object';
					propcol.forEach(prop => {
						if (isobj)
							obj[prop] = val[prop];
						else
							obj[prop] = null;
					});
					result.push(obj);
				}
				resolve(result);
			}
			else
				resolve(null);
		});
	}


	/*
	computeInitialValue() {
		var source = this;
		return new Promise(function x1(resolve, reject) {
			source.inited = true;
			resolve();
		});
	}
	*/

	recompute(doFireEvent = true) {
		const source = this;
		return new Promise((resolve) => {
			source.inited = true;
			if (doFireEvent)
				source.fireEvent('changed');
			/*
			if (!doNotUpdateWidgets)
				source.fireEvent('changed');
				*/
			resolve();
		});
	}

	async setValue(property, value, doFireEvent = true) {
		const source = this;
		if (property == null) {
			value = source.convertAs(source.dataType, value);
			if (source.parentSource != null) {
				if (source.properyInParent != null) {
					source.parentSource.setValue(source.properyInParent, value);
				}
			} else {
				source.value = value;
				source.touch();
			}
		}
		else if (source.parentSource != null) {
			if (source.properyInParent != null) {
				source.parentSource.getValue(source.properyInParent).then((result) => {
					if (result != null && result instanceof Object) {
						result[property] = value;
						source.touch();
					}
				});
			}
		} else if (source.value != null && source.value instanceof Object) {
			source.value[property] = value;
			source.touch();
		}

		await source.recomputeChildren(doFireEvent);
		if (doFireEvent)
			source.fireEvent('changed');
		/*
		source.recomputeChildren(doFireEvent).then(() => {
			if (doFireEvent)
				source.fireEvent('changed');
		});
		*/

		return null;
	}

	async setValueFromMethodResult(resp, doFireEvent = true) {
		const { result } = resp;
		await this.setValue(null, result, doFireEvent);
		return null;
	}

	// eslint-disable-next-line class-methods-use-this
	convertAs(dataType, value) {
		if (value === undefined)
			value = null;
		if (dataType == null || value == null)
			return null;

		if (dataType === 'string') {
			if (value instanceof Date) {
				return value.toDateString();
			}
			if (value instanceof Object) {
				return JSON.stringify(value, ' ', 2);
			}
			return String(value);
		} if (dataType === 'number') {
			return parseFloat(value);
		} if (dataType === 'bool') {
			return Boolean(value);
		} if (dataType === 'date') {
			if (typeof value === 'string') {
				return Utils.parseDate(value);
			}
			return new Date(value);

		} if (dataType === 'object') {
			if (typeof value === 'string') {
				let result = null;
				try {
					result = JSON.parse(value);
				} catch (err) {
					result = null;
				}
				return result;
			} return value;
		}
		return value;


	}

	formatValue(value, formatStr) {
		if (formatStr == null) {
			return this.convertAs('string', value);
		} if (formatStr === '!!') {
			return Utils.formatValueForDateInput(value);
		}
		// return this.convertAs("string", value);
		if (value == null)
			return '';
		if (typeof (value) === 'number') {
			return Utils.formatNumber(value, { format: formatStr });
		} if (value instanceof Date) {
			return Utils.formatDate(value, { format: formatStr });
		}
		return Utils.formatString(value, { format: formatStr });



	}
}

module.exports = {
	DataSourceOnScalar,
};
