
// ----------------------

const { DataSource } = require('./datasource');
// const { dlog } = require('./tools');

class DataSourceOnCatalog extends DataSource {
	constructor(namespace, id, catalog) {
		super(namespace, id, 'catalog');
		this.catalog = catalog;
	}

	clone(newid) {
		const clonedSource = new DataSourceOnCatalog(this.namespace, newid, this.catalog);
		return clonedSource;
	}

	// eslint-disable-next-line class-methods-use-this
	buildCallFuncURL() {
		return '$catalog';
	}

	getCatalog() {
		return this.catalog;
	}

}


module.exports = {
	DataSourceOnCatalog,
};
