// ----------------------

const { DataSource } = require('./datasource');

class DataSourceOnEntitySel extends DataSource {
	constructor(namespace, id, type, dataclass, parentID, initialValue, catalog, properyInParent) {
		super(namespace, id, type);
		this.catalog = catalog;
		this.dataclassID = dataclass;
		this.dataclass = catalog.getDataClass(dataclass);
		if (parentID == null)
			this.parentSource = null;
		else {
			this.parentSource = DataSource.getSource(parentID, namespace);
			if (this.parentSource != null && properyInParent != null) {
				if (this.parentSource.type === 'entity')
					this.properyInParent = properyInParent;
			}
		}
		this.entitysel = null;
		this.initialValue = initialValue;
	}

	clone(newid) {
		const clonedSource = new DataSourceOnEntitySel(this.namespace, newid, this.type, this.dataclassID, this.parendID, this.initialValue, this.catalog, this.properyInParent);
		return clonedSource;
	}

	// eslint-disable-next-line class-methods-use-this
	getDataType(property) {
		let result = null;
		if (property === 'length')
			result = 'number';
		return result;
	}

	async getValueAsParam(property, settings) {
		const source = this;
		return new Promise(function x6(resolve, reject) {
			source.getValue(property, settings).then((sel) => {
				if (sel == null) {
					resolve(null);
				} else {
					const result = sel.getDescriptorAsParam();
					resolve(result);
				}

			}).catch(reject);

		});
	}

	async getValue(property, settings) {
		const source = this;
		return new Promise(function x5(resolve, reject) {
			let canresolve = true;
			let result = null;
			if (source.entitysel != null) {
				if (property === 'length') {
					canresolve = false;
					source.entitysel.getLength(settings).then((len)=> {
						resolve(len);
					}).catch(reject);
				}
				else if (property == null) {
					result = source.entitysel;
				}
				else if (typeof(property) === 'number') {
					canresolve = false;
					source.entitysel.getEntity(property, settings).then(function r6(entity) {
						resolve(entity);
					}).catch(reject);
				}
			}
			if (canresolve)
				resolve(result);
		});
	}

	async getCollection(index, howmany, propertyStr) {
		const source = this;
		return new Promise(function xcol5(resolve, reject) {
			if (source.entitysel != null) {
				source.entitysel.getCollection(index, howmany, propertyStr).then(collection => {
					resolve(collection);
				}).catch(reject);
			}
			else
				resolve(null);
		});
	}

	async orderBy(attributes) {
		const source = this;
		return new Promise(async function orderBySourceSel(resolve ,reject) {
			if (source.entitysel != null) {
				try {
					const newsel = await source.entitysel.orderBy(attributes);
					if (newsel != null) {
						source.setSelection(newsel);
						resolve(newsel);
					}
				}
				catch (err) {
					reject(err);
				}
			} else {
				resolve(null);
			}
		});
	}


	/*
	computeInitialValue() {
		var source = this;
		return new Promise(function x4(resolve, reject) {
			var canresolveToNull = true;
			if (source.initialValue != null) {
				if (source.initialValue == 'all') {
					canresolveToNull = false;
					source.entitysel = source.dataclass.allEntities({ filterAttributes: source.filterAttributesText });
					source.inited = true;
					resolve();
				}
			} else if (source.parentSource != null && source.parentSource.type == 'entity') {
				canresolveToNull = false;
				source.parentSource.getValue(source.properyInParent).then(function (resultSel) {
					source.entitysel = resultSel;
					source.inited = true;
					resolve();
				}).catch(reject);
			}

			if (canresolveToNull) {
				source.inited = true;
				resolve();
			}
		});
	}
	*/

	recompute(doFireEvent = true) {
		const source = this;
		return new Promise(function x3(resolve, reject) {
			if (source.parentSource != null && source.parentSource.type === 'entity') {
				source.parentSource.getValue(source.properyInParent).then((resultSel) => {
					source.entitysel = resultSel;
					source.inited = true;
					if (doFireEvent)
						source.fireEvent('changed');
					resolve();
				}).catch(reject);
			}
			else if (source.initialValue != null && !source.parentSource && !source.inited) {
				if (source.initialValue === 'all') {
					source.entitysel = source.dataclass.allEntities({ filterAttributes: source.filterAttributesText });
					source.inited = true;
					if (doFireEvent)
						source.fireEvent('changed');
					resolve();
				} else {
					source.inited = true;
					resolve();
				}
			}
			else {
				source.inited = true;
				resolve();
			}
		});
	}

	async setValue(property, value, doFireEvent = true) {
		const source = this;
		if (property == null) {
			source.entitysel = value;

			await source.recomputeChildren(doFireEvent);
			if (doFireEvent)
				source.fireEvent('changed');
			/*
			source.recomputeChildren(doFireEvent).then(() => {
				if (doFireEvent)
					source.fireEvent('changed');
			});
			*/

		}
	}

	async setValueFromMethodResult(resp, doFireEvent = true) {
		if (this.dataclass != null) {
			if (resp.__ENTITIES != null) {
				if (this.dataclass.getName() === resp.__DATACLASS) {
					// eslint-disable-next-line
					const { EntitySelection } = require('./entitysel');

					const sel = new EntitySelection(this.dataclass, { filterAttributes: this.filterAttributesText }, resp);
					await this.setValue(null, sel, doFireEvent);
				} else {
					await this.setValue(null, null, doFireEvent);
				}
			} else {
				await this.setValue(null, null, doFireEvent);
			}

		}
		return null;
	}

	buildCallFuncURL() {
		const { url, added } = this.entitysel.getComputedURL(null, true);
		return url;
	}

	getSelection() {
		return this.entitysel;
	}

	setSelection(sel, doFireEvent = true) {
		const source = this;
		source.entitysel = sel;

		source.recomputeChildren().then(() => {
			if (doFireEvent)
				source.fireEvent('changed');
		});

	}
}


module.exports = {
	DataSourceOnEntitySel,
};
