/* eslint-disable no-plusplus */
// ------------------------------------------------------------- DataProvider

const { restRequest } = require('./rest');
const { DataClass } = require('./dataclass');
const { htmlEncode } = require('./htmlEncode');

// ----------------------------- DataStore

function create_UUID() {
	let dt = new Date().getTime();
	const uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function xx(c) {
		// eslint-disable-next-line no-bitwise
		const r = (dt + Math.random() * 16) % 16 | 0;
		dt = Math.floor(dt / 16);
		// eslint-disable-next-line no-bitwise
		return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
	});
	return uuid;
}


class ProgressIndicator {
	constructor() {
		this.refID = create_UUID();
		this.started = new Date();
		this.lastChecked = this.started.getTime();
		this.inited = false;
		this.checkDeletedCount = 0;
		ProgressIndicator.register(this);
		this.askedToStop = false;
		this.graceFullStop = false;
	}

	getRefID() {
		return this.refID;
	}

	userAbort() {
		this.askedToStop = true;
		// will be handled in the interval
	}

	stopGracefully() {
		this.askedToStop = true;
		this.graceFullStop = true;
	}

	static InitManager() {
		ProgressIndicator.progresses = {};
		ProgressIndicator.lastTime = new Date().getTime();
		ProgressIndicator.nextListenerID = 0;
		ProgressIndicator.listeners = {};

		ProgressIndicator.interval = setInterval(() => {
			const now = new Date().getTime();
			// const delay = ProgressIndicator.lastTime - now;

			const ids = Object.values(ProgressIndicator.progresses).map((progress) => {
				if (progress.askedToStop)
					return `--${progress.getRefID()}`;
				return progress.getRefID();
			}).join(',');
			if (ids.length !== 0) {

				restRequest('get', `/rest/$progressWebInfo/${ids}`).then((resp) => {
					if (Array.isArray(resp)) {
						resp.forEach((progressInfo) => {
							const {refID} = progressInfo;
							const progress = ProgressIndicator.getProgress(refID);
							if (progressInfo.deleted) {
								if (progress != null) {
									let mustDelete = false;
									if (progress.inited)
										mustDelete = true;
									else {
										progress.checkDeletedCount++;
										if (progress.checkDeletedCount > 10)
											mustDelete = true;
									}
									if (mustDelete) {
										ProgressIndicator.unregister(progress);
										ProgressIndicator.tellListeners('remove', progress);
									}
								}
							} else if (progress != null) {

								let session = null;
								let title = progressInfo.Title;
								if (progressInfo.SessionInfo != null && progressInfo.SessionInfo.length > 0) {
									session = progressInfo.SessionInfo[progressInfo.SessionInfo.length - 1];
								}
								if (progressInfo.SessionInfo != null && progressInfo.SessionInfo.length > 1) {
									title = progressInfo.SessionInfo[progressInfo.SessionInfo.length - 2].fMessage;
								}
								if (session != null) {
									progress.inited = true;
									progress.title = title;
									progress.curValue = session.fValue;
									progress.maxValue = session.fMax;
									progress.message = session.fMessage;
								}

							}
						});
					}
				}).catch((err) => {
					if (err.status === -2) {
						Object.values(ProgressIndicator.progresses).forEach((progress) => {
							if (progress != null) {
								ProgressIndicator.unregister(progress);
								ProgressIndicator.tellListeners('remove', progress);
							}
						});
					}
				});
			}

			Object.values(ProgressIndicator.progresses).forEach((progress) => {
				if (progress.inited) {
					ProgressIndicator.tellListeners('update', progress);
				}
			});


			ProgressIndicator.lastTime = now;
		}, 1000);

	}

	static register(progress) {
		ProgressIndicator.progresses[progress.getRefID()] = progress;
	}

	static unregister(progress) {
		delete ProgressIndicator.progresses[progress.getRefID()];
	}

	static getProgress(refID) {
		return ProgressIndicator.progresses[refID];
	}

	static addListener(handler) {
		++ProgressIndicator.nextListenerID;
		ProgressIndicator.listeners[`l${ProgressIndicator.nextListenerID}`] = handler;
		return ProgressIndicator.nextListenerID;
	}

	static removeListener(id) {
		delete ProgressIndicator.listeners[`l${id}`];
	}

	static tellListeners(command, progress) {
		Object.values(ProgressIndicator.listeners).forEach((handler) => {
			handler(command, progress);
		});
	}
}


class DataStore {
	constructor(ID) {
		if (ID == null || ID === '')
			this.url = '/rest/$catalog/$all';
		else
			this.url = `/rest/$catalog/${  ID  }/$all`;

		this._private = {
			uniqID: null,
			dataclasses: {},
			dataclassesByCollectionName: {},
			cacheDelay: 60 * 1000, // 1 min
		};
	}

	fetch() {
		const {url} = this;
		const datastore = this;
		return new Promise((resolve, reject) => {
			restRequest('get', url).then((resp) => {
				const dataclasses = resp.dataClasses;
				if (resp.__UNIQID != null)
					datastore._private.uniqID = resp.__UNIQID;
				if (resp.__BASEID != null)
					datastore._private.baseID = resp.__BASEID;
				if (dataclasses != null) {
					dataclasses.forEach((dataclassDef) => {
						const dataclass = new DataClass(dataclassDef, datastore);
						datastore[dataclass.getName()] = dataclass;
						datastore._private.dataclasses[dataclass.getName()] = dataclass;
						datastore._private.dataclassesByCollectionName[dataclass.getCollectionName()] = dataclass;
					});
				}
				resolve(datastore);
			}).catch(reject);
		});
	}

	getUniqID() {
		return this._private.uniqID;
	}

	getBaseID() {
		return this._private.baseID;
	}

	getDataClass(classname) {
		return this[classname];
	}

	getDataClassByCollectionName(collectionName) {
		return this._private.dataclassesByCollectionName[collectionName];
	}

	getCacheDelay() {
		return this._private.cacheDelay;
	}

	getDataClasses() {
		return this._private.dataclasses;
	}

	// eslint-disable-next-line class-methods-use-this
	initProgressManager() {
		ProgressIndicator.InitManager();
	}

	// eslint-disable-next-line class-methods-use-this
	newProgressIndicator() {
		return new ProgressIndicator();
	}

	// eslint-disable-next-line class-methods-use-this
	addProgressListener(handler) {
		ProgressIndicator.addListener(handler);
	}

	// eslint-disable-next-line class-methods-use-this
	removeProgressListener(id) {
		ProgressIndicator.removeListener(id);
	}

	// eslint-disable-next-line class-methods-use-this
	HTMLEncode(source, display, tabs) {
		return htmlEncode(source, display, tabs);
	}

}

module.exports = {
	DataStore,
};
