// ------------------------------------------------------------- DataSources
const { Method4D } = require('./method');

// test

class AttributeFilter {
	constructor() {
		this.filter = {};
	}

	addAttribute(attname, subfilter) {
		if (subfilter == null) {
			this.filter[attname] = true;
		} else {
			this.filter[attname] = subfilter;
		}
	}

	mergeWith(otherfilter) {
		Object.keys(otherfilter.filter).forEach((e) => {
			const left = this.filter[e];
			const right = otherfilter.filter[e];
			if (typeof left === 'object' && typeof right === 'object' && left != null && right != null)
				left.mergeWith(right);
			else
				this.filter[e] = right;
		});
	}

	// eslint-disable-next-line class-methods-use-this
	addFilterText(filterText, newprop) {
		if (filterText !== '')
			return `${filterText},${newprop}`;
		return newprop;
	}

	buildString(curpath) {
		let result = '';
		Object.keys(this.filter).forEach((e) => {
			const elem = this.filter[e];
			result = this.addFilterText(result, curpath + e);
			if (elem instanceof Object) {
				result = this.addFilterText(result, elem.buildString(`${curpath + e}.`));
			}
		});
		return result;
	}
}

class DataSource {
	constructor(namespace, id, type) {
		this.id = id;
		this.type = type;

		this.stamp = 0;
		this.frozenStamp = 0;
		this.inited = false;
		this.beeingResolved = false;
		this.parentSource = null;
		this.children = {};
		// this.widgets = {};
		this.fromServerStorage = false;
		this.canBeUpdatedByParent = true;
		this.namespace = namespace;
		this.local = false;
	}

	// ---------------- static functions

	static getOrBuildNameSpace(namespace) {
		if (DataSource.spaces[namespace] == null) {
			DataSource.spaces[namespace] = { sources: {}};
		}
		return DataSource.spaces[namespace];
	}

	static getNameSpace(namespace) {
		return DataSource.spaces[namespace];
	}

	static buildSource(sourcestr, initialValue, ds, namespace = '__default') {
		const space = DataSource.getOrBuildNameSpace(namespace);
		if (sourcestr == null) return;

		// eslint-disable-next-line global-require
		const { DataSourceOnServerStorage } = require('./datasourceStorage');
		// eslint-disable-next-line global-require
		const { DataSourceOnScalar } = require('./datasourceScalar');
		// eslint-disable-next-line global-require
		const { DataSourceOnEntity } = require('./datasourceEntity');
		// eslint-disable-next-line global-require
		const { DataSourceOnEntitySel } = require('./datasourceEntitysel');
		// eslint-disable-next-line global-require
		const { DataSourceOnCatalog } = require('./datasourceCatalog');
		// eslint-disable-next-line global-require
		const { DataSourceOnDataClass } = require('./datasourceDataClass');

		const multiSources = sourcestr.split(',');
		let sourcestrCopy = sourcestr;
		if (multiSources.length > 1) {
			multiSources.forEach((oneSourceStr) => {
				DataSource.buildSource(oneSourceStr, null, ds, namespace);
			});
		} else if (sourcestrCopy !== '') {
			let curparent = null;
			let dataType = null;
			let dataTypestr = null;
			let dataclassOrSel = null;
			const firstsplit = sourcestrCopy.split(':');
			if (firstsplit.length > 1) {
				[sourcestrCopy] = firstsplit;
				dataTypestr = firstsplit[1];
				dataType = DataSource.mapDataType(dataTypestr);
				if (firstsplit.length > 2) dataclassOrSel = firstsplit[2];
			}
			if (space.sources[sourcestrCopy] == null) {
				// if does not exists yet
				const parts = sourcestrCopy.split('.');
				if (parts.length > 1) {
					let sname = '';
					const nbparent = parts.length;
					for (let i = 0; i < nbparent; i += 1) {
						if (i === 0) {
							sname = parts[i];
							let psource = space.sources[sname];
							if (psource == null && sname === 'store') {
								psource = new DataSourceOnServerStorage(namespace, sname, 'serverStorage', ds, dataType);
								space.sources[sname] = psource;
							}
							if (psource == null && sname === 'ds') {
								psource = new DataSourceOnCatalog(namespace, 'ds', ds);
								space.sources[sname] = psource;
							}

							if (psource == null) {
								console.error(`base source is missing to build:${sourcestrCopy}`);
								break;
							} else {
								curparent = psource;
							}
						} else {
							sname += `.${parts[i]}`;
							const psource = space.sources[sname];
							if (psource != null) {
								curparent = psource;
							} else if (curparent.type === 'catalog') {
								const catalog = curparent.getCatalog();
								const dataclass = catalog.getDataClass(parts[i]);
								if (dataclass == null) {
									console.error(`dataclass ${parts[i]} does not exists for: ${sourcestrCopy}`);
									break;
								} else {
									const source = new DataSourceOnDataClass(namespace, sname, dataclass, catalog, curparent.id, parts[i]);
									space.sources[sname] = source;
									curparent = source;
								}
							} else if (curparent.type === 'entity') {
								const { dataclass } = curparent;
								if (dataclass == null) {
									console.error(`dataclass is missing to build:${sourcestrCopy}`);
									break;
								} else {
									const att = dataclass.getAttribute(parts[i]);
									if (att == null) {
										console.error(`attribute:${parts[i]} not found to build:${sourcestrCopy}`);
										break;
									} else if (att.kind === 'storage' || att.kind === 'calculated' || att.kind === 'alias') {
										const source = new DataSourceOnScalar(namespace, sname, 'scalar', parts[i], curparent);
										source.setLocal(false);
										space.sources[sname] = source;
										curparent = source;
									} else if (att.kind === 'relatedEntity') {
										const relDataClass = ds.getDataClass(att.type);
										const source = new DataSourceOnEntity(namespace, sname, 'entity', relDataClass.getName(), curparent.id, null, ds, parts[i]);
										space.sources[sname] = source;
										curparent = source;
									} else if (att.kind === 'relatedEntities') {
										const relDataClass = ds.getDataClassByCollectionName(att.type);
										const source = new DataSourceOnEntitySel(namespace, sname, 'entitysel', relDataClass.getName(), curparent.id, null, ds, parts[i]);
										space.sources[sname] = source;
										curparent = source;
									}
								}
							} else if (curparent.type === 'entitysel' && parts[i] === '[]') {
								if (curparent.dataclass == null) {
									console.error(`dataclass parent is invalid for: ${sourcestrCopy}`);
									break;
								}
								const source = new DataSourceOnEntity(namespace, sname, 'entity', curparent.dataclass.getName(), curparent.id, null, ds);
								space.sources[sname] = source;
								curparent = source;
							} else {
								const source = new DataSourceOnScalar(namespace, sname, 'scalar', parts[i], curparent, null, dataType);
								space.sources[sname] = source;
								if (curparent == null)
									source.setLocal(true);
								else
									source.setLocal(curparent.isLocal());
								curparent = source;
							}
						}
					}
				} else if (sourcestrCopy === 'ds') {
					const source = new DataSourceOnCatalog(namespace, 'ds', ds);
					space.sources[sourcestrCopy] = source;
					curparent = source;
				} else if (sourcestrCopy === 'store') {
					const source = new DataSourceOnServerStorage(namespace, sourcestrCopy, 'serverStorage', ds, dataType);
					space.sources[sourcestrCopy] = source;
					curparent = source;
				} else if (dataTypestr === 'entitysel') {
					if (dataclassOrSel != null) {
						const source = new DataSourceOnEntitySel(namespace, sourcestrCopy, dataTypestr, dataclassOrSel, null, 'all', ds);
						space.sources[sourcestrCopy] = source;
						curparent = source;
					}
				} else if (dataTypestr === 'entity') {
					if (dataclassOrSel != null) {
						const dsel = DataSource.getSource(dataclassOrSel, namespace);
						if (dsel != null && dsel.type === 'entitysel' && dsel.dataclass != null) {
							const dataclassName = dsel.dataclass.getName();
							const source = new DataSourceOnEntity(namespace, sourcestrCopy, dataTypestr, dataclassName, dataclassOrSel, 'first', ds);
							space.sources[sourcestrCopy] = source;
							curparent = source;
						}
					}
				} else {
					// simple datasource
					const source = new DataSourceOnScalar(namespace, sourcestrCopy, 'scalar', null, null, null, dataType);
					space.sources[sourcestrCopy] = source;
					source.setLocal(true);
					curparent = source;
				}
			}
		}

		const ev = new Event('datasource:created');
		ev.id = sourcestr;
		window.dispatchEvent(ev);
	}

	static create({ id, type, dataclass, initialValue, from, dataType }, ds, namespace = '__default') {
		// eslint-disable-next-line global-require
		const { DataSourceOnEntity } = require('./datasourceEntity');
		// eslint-disable-next-line global-require
		const { DataSourceOnEntitySel } = require('./datasourceEntitysel');
		// eslint-disable-next-line global-require
		const { DataSourceOnScalar } = require('./datasourceScalar');
		let datasource = DataSource.getSource(id, namespace);
		if (datasource == null) {
			let pid = null;
			if (from != null && from !== '')
				pid = from;

			if (from != null && from.indexOf('.') >= 0)
				type = 'auto';

			let ev = new Event('datasource:created');

			switch (type) {
				case 'entity':
					datasource = new DataSourceOnEntity(namespace, id, type, dataclass, pid, initialValue, ds);
					window.dispatchEvent(ev);
					break;
				case 'entitysel':
					datasource = new DataSourceOnEntitySel(namespace, id, type, dataclass, pid, initialValue, ds);
					window.dispatchEvent(ev);
					break;
				case 'auto':
					datasource = DataSource.getSourceOrBuild(from, initialValue, ds, namespace);
					DataSource.AddAlias(id, datasource, namespace);
					break;
				case 'scalar':
					datasource = new DataSourceOnScalar(namespace, id, 'scalar', null, null, initialValue, dataType);
					window.dispatchEvent(ev);
					break;
				case 'array':
					initialValue = Array.isArray(initialValue) ? initialValue: [];
					datasource = new DataSourceOnScalar(namespace, id, 'scalar', null, null, initialValue, 'array');
					window.dispatchEvent(ev);
					break;
				case 'object':
					datasource = new DataSourceOnScalar(namespace, id, 'scalar', null, null, initialValue, 'object');
					window.dispatchEvent(ev);
					break;
				default:
					break;
			}

			const space = DataSource.getOrBuildNameSpace(namespace);

			if (datasource != null) {
				space.sources[datasource.id] = datasource;
			}
		}
		return datasource;
	}

	// eslint-disable-next-line no-unused-vars
	static getSourceAttributes(id, options = null, namespace = '__default') {
		let atts = [];
		const source = DataSource.getSource(id, namespace);
		if (source.dataclass != null) {
			atts = source.dataclass.getAllAttributes();
		}
		return atts;
	}

	// -------------------------------------- non static member functions

	isLocal() {
		return this.local;
	}

	setLocal(local) {
		this.local = local;
	}

	buildCallFuncURL(/* methodName */) {
		return '';
	}


	fireEvent(eventType, data) {
		const ev = new window.Event(`datasource:${eventType}:${this.id}`);
		ev.data = data;
		// ev.initiator = this;
		window.dispatchEvent(ev);
	}

	// eslint-disable-next-line class-methods-use-this
	addListener(eventType, callBack) {
		window.addEventListener(`datasource:${eventType}:${this.id}`, callBack);
	}

	// eslint-disable-next-line class-methods-use-this
	async getValue(/* property, settings */) {
		return new Promise(function x15(resolve) {
			resolve(null);
		});
	}

	async getCollection(/* index, howmany, propertyStr */) {
		return new Promise(function xcol15(resolve) {
			resolve(null);
		});
	}

	async orderBy(/* attributes */) {
		return new Promise(function orderByBasic(resolve /* , reject */) {
			resolve(null);
		});
	}

	async getValueAsParam(property, settings) {
		return this.getValue(property, settings);
	}

	// eslint-disable-next-line class-methods-use-this
	getDataType(/* property */) {
		return null;
	}

	// eslint-disable-next-line class-methods-use-this
	setValue(/* property, value, doFireEvent = true */) {
		return null;
	}

	// eslint-disable-next-line no-unused-vars
	async setValueFromMethodResult(resp, doFireEvent = true) {
		return null;
	}

	clone(/* newid */) {
		return null;
	}

	recompute(/* doFireEvent = true */) {
		const source = this;
		return new Promise(function x11(resolve) {
			source.inited = true;
			resolve();
		});
	}

	async recomputeChildren(doFireEvent = true) {
		const source = this;
		return new Promise(function sourceRecompChildren(resolve, reject) {
			try {
				Object.keys(source.children).forEach(async (e) => {
					const child = source.children[e];
					if (child.canBeUpdatedByParent) {
						await child.recompute(doFireEvent);
						await child.recomputeChildren(doFireEvent);
					}
				});
				resolve();
			}
			catch (error) {
				reject(error);
			}
		});

	}


	addAttributeFilter(child) {
		const source = this;
		const dataclass = this.getDataClass();

		if (source.type === 'entity') {
			if (child.properyInParent != null) {
				source.filterAttributes.addAttribute(child.properyInParent, child.filterAttributes);
			}
		} else if (source.type === 'entitysel') {
			if (child.filterAttributes != null) {
				source.filterAttributes.mergeWith(child.filterAttributes);
			}
			if (child.properyInParent === 'length') {
				if (source.dataclass != null) {
					source.filterAttributes.addAttribute(dataclass.getKeys().join(','));
				}
			}
		} else if (source.fromServerStorage) {
			if (child.properyInParent != null) {
				source.filterAttributes.addAttribute(child.properyInParent, child.filterAttributes);
			}
		}
	}

	createRequestOptimization() {
		const source = this;
		if (source.type === 'entity' || source.type === 'entitysel' || source.fromServerStorage) {
			source.filterAttributes = new AttributeFilter();
			Object.keys(source.children).forEach((e) => {
				const child = source.children[e];
				child.createRequestOptimization();
			});
			source.filterAttributesText = source.filterAttributes.buildString('');
		}
		else if (source.type === 'root') {
			Object.keys(source.children).forEach((e) => {
				const child = source.children[e];
				child.createRequestOptimization();
			});
		}
		if (source.parentSource != null) {
			source.parentSource.addAttributeFilter(source);
		}

	}

	// eslint-disable-next-line class-methods-use-this
	formatValue(value/* , formatStr */) { // need to be extended in sub classes
		return value;
	}

	getStamp() {
		return this.stamp;
	}

	touch() {
		this.stamp += 1;
	}

	getParentSource() {
		return this.parentSource;
	}

	getDataClass() {
		return this.dataclass;
	}

	getProperyInParent() {
		return this.properyInParent;
	}

	freezeStamp(withChildren = false) {
		this.frozenStamp = this.stamp;
		const source = this;
		if (withChildren) {
			Object.keys(source.children).forEach((e) => {
				source.children[e].freezeStamp(true);
			});
		}
	}

	modifiedSinceLastFreeze() {
		return this.stamp !== this.frozenStamp;
	}

	dumpTree(curtext, level) {
		let text = curtext;
		for (let i = 0; i < level; i += 1)
			text += '- ';
		text += `${this.id} (${this.type}) \r\n`;

		Object.keys(this.children).forEach((e) => {
			text = this.children[e].dumpTree(text, level + 1);
		});
		return text;
	}


	// ----------------------------- more static functions

	static init() {
		DataSource.spaces = { '__default': { sources: {} } };
		DataSource.dataTypeMap = {};
		DataSource.globalStamp = 1;
		DataSource.anonymCount = 1;

		DataSource.dataTypeMap = {
			'bool': 'bool',
			'string': 'string',
			'text': 'string',
			'uuid': 'string',
			'short': 'number',
			'long': 'number',
			'number': 'number',
			'long64': 'number',
			'duration': 'number',
			'object': 'object',
			'date': 'date',
		};
	}

	static getSource(id, namespace = '__default') {
		const space = DataSource.getNameSpace(namespace);
		if (space == null)
			return null;
		return space.sources[id];
	}

	static getSourceOrBuild(id, initialValue, ds, namespace = '__default') {
		if (id == null)
			return null;
		DataSource.buildSource(id, initialValue, ds, namespace);
		const [trueID] = id.split(':');
		return DataSource.getSource(trueID, namespace);
	}

	static AddAlias(id, datasource, namespace = '__default') {
		if (id != null && id !== '') {
			DataSource.getOrBuildNameSpace(namespace).sources[id] = datasource;
		}
	}

	static replaceDataSource(obj, prop, elemSource, newid) {
		const original = obj[prop + '-original'];
		if (original == null)
			obj[prop + '-original'] = obj[prop];
		else
			obj[prop] = obj[prop + '-original'];

		const existingOne = obj[prop];
		if (existingOne != null && existingOne !== '')
			obj[prop] = DataSource.changePath(existingOne, elemSource, newid);
	}

	static changePath(existingPath, elemSource, newid) {
		let result = existingPath;
		if (existingPath != null && elemSource != null && newid != null && existingPath.startsWith(elemSource)) {
			result = newid + existingPath.substring(elemSource.length);
		}
		return result;
	}

	static getSources(sourceList, namespace = '__default') {
		const result = [];
		const list = sourceList.split(',');
		list.forEach((elem) => {
			const sourcestr = elem.split(':')[0];
			const source = DataSource.getSource(sourcestr, namespace);
			result.push(source); // can be null
		});
		return result;
	}

	static getAllSources(namespace = '__default') {
		const space = DataSource.getNameSpace(namespace);
		if (space == null)
			return null;
		return space.sources;
	}

	static mapDataType(datatype) {
		const result = DataSource.dataTypeMap[datatype];
		return result;
	}

	static buildDependencies(namespace = '__default') {
		const space = DataSource.getOrBuildNameSpace(namespace);

		const datasourcesNames = Object.keys(space.sources);
		const root = space.root || new DataSource(namespace, '$$root', 'root');

		datasourcesNames.forEach((e) => {
			const source = space.sources[e];

			if (!source) {
				return;
			}

			if (!source.parentSource) {
				root.children[source.id] = source;
			}
			else {
				source.parentSource.children[source.id] = source;
			}
		});

		space.root = root;
	}


	static createRequestOptimization(namespace = '__default') {
		const space = DataSource.getNameSpace(namespace);
		if (space != null)
			space.root.createRequestOptimization();
	}

	static dumpTree(namespace = '__default') {
		const space = DataSource.getNameSpace(namespace);
		if (space == null)
			return '';
		return space.root.dumpTree('', 0);
	}


	static computeInitialValues(namespace = '__default') {
		const space = DataSource.getNameSpace(namespace);
		if (space != null)
			space.root.recomputeChildren();
	}


	static initServerStorage() {
		return new Promise((resolve) => {
			const source = DataSource.getSource('store');
			if (source == null) {
				resolve();
			} else {
				const method = new Method4D('$$', null, null);
				method.callServerSide(null, null).then(() => {
					resolve();
				});
			}
		});
	}

	static getFormNameSpace(formioElement) {
		let namespace = formioElement.root.options.namespace;
		if (namespace == null || namespace === '')
			namespace = '__default';
		return namespace;
	}

}


module.exports = {
	DataSource,
	AttributeFilter,
};
